import { useEffect, useState } from "react";
import Fp35 from "./fp35";
import Fp36 from "./fp36";
import Rss9__10 from "./rss9_10";

const RUQ = ({ appData, setAppData, terms }) => {

    const saveData = () => {
        appData.RUQ = {
            records,
            score:null
        }
    }

    const questionsGroup = (quizString) =>
        quizString.split(',').map(
            (item,index) => { 
                let term = item.split('/')[0]
                let answer = item.split('/')[1]
                let answerTerm = term.split('.')
                answerTerm[2] = answerTerm[2].replace('Q','A')
                answerTerm = answerTerm.join('.')
    
                let result = { term, answer, answerTerm }
    
                if(index > 0){
                    result.options = [
                        {term:'core.True',value:true},
                        {term:'core.False',value:false}
                    ]
                }else{
                    result.options = Object.keys(terms)
                    .filter(t => t.includes(`${term}.Opt`))
                    .sort()
                    .map((opt,key) => ({term:opt,value:key+1}))
                }
                return result
        })
    

    const quizString = terms[`RUQ`]
    const [quizQuestions,setQuizQuestions] = useState([])

    const [question, setQuestion] = useState(undefined);
    const [lastRecord, setLastRecord] = useState(undefined);
    
    const [answerFeedback, setAnswerFeedback] = useState(false);
    const [records, setRecords] = useState([]);

    useEffect(() => {
        let quiz = questionsGroup(quizString)
        setQuestion(quiz.shift())
        setQuizQuestions(quiz)
    }, []);

    const addAnswer = (data) => {
        setLastRecord(data);
        setRecords(records.concat(data));
        setAnswerFeedback(question);
    };

    const nextQuestion = () => {
        setQuestion(quizQuestions.shift());
        setAnswerFeedback(false);
    };

    // RENDER //
    if(appData.RUQ.score){

        return <Rss9__10 terms={terms} records={appData.RUQ.records} appData={appData} setAppData={setAppData} />

    }else if (question && !answerFeedback) {      //NEXT QUESTION
        
        return <Fp35 appData={appData} terms={terms} question={question} addAnswer={addAnswer} />

    } else if (question && answerFeedback){ // ANSWER
        
        return <Fp36 appData={appData} terms={terms} question={question} record={lastRecord} nextQuestion={nextQuestion} />

    } else if (!question && records.length > 0 && quizQuestions.length === 0){    // CLOSING SCREEN
        /* saveData() */
        return <Rss9__10 terms={terms} records={records} appData={appData} setAppData={setAppData} />

    }else{
        return (
            <>
                <div className="cur_cc05cc03">
                    <div className="bk_left"></div>
                    <div className="bk_right"></div>
                </div>
                <div className="lyts">
                    <div className="grd_cont">
                        
                    </div>
                </div>
            </>
        )
    }

    
};

export default RUQ;
