import React from "react"
import LogOut from '../components/btns/logOut'
import { ReactComponent as Meg } from '../assets/images/icons/campaign.svg'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"


const Fp1 = ({ terms, appData, setAppData }) => {

    return (
        <>
            <div className="cur_cc04">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp1">
                        <div className="grd_left">
                            <div className="jello_vertical"><Meg /></div>
                        </div>
                        <div className="grd_right">
                            <h1 className="tracking_in_expand">{terms['MMBQ.Intro']}</h1>
                            <h3 className="tracking_in_expand">{terms['MMBQ.Intro.QuizIntro']}</h3>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={appData.lastScreen || "/Ss8_1_1"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="280px"
                                    height="48px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right">
                            <Link to={"/MMBQ"}>
                                <Btn text_btn={terms['core.Next']} />
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp1;