import React, { useEffect } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import { Link } from "react-router-dom"
import { ReactComponent as Congr } from '../assets/images/icons/congratulations.svg'

const Rss9__10 = ({ terms, records, appData,setAppData }) => {

    let score = records.reduce((prev, curr) => prev + curr.score, 0);

    const nextActions = {
        highScore : [
            {term:'RSS.ExploreMenu', link:'/Rss11'},
            {term:'RSS.ExploreLearnMore', link:'/Ss8_1'}
        ],
        lowScore : [
            {term:'RSS.LowScore.Refresher', link:'/Ss2'},
            {term:'RSS.ScoreLow.VisitSS', link:'/Rss11'},
            {term:'RSS.ScoreLow.FP', link:'/Ss8_1'}
        ]
    }[score<2?'lowScore':'highScore'].map((action,key) => <Link to={action.link} key={key}><Btn text_btn={terms[action.term]} /></Link> )

    useEffect(() => {
        appData.RUQ = { score, records }
        setAppData(appData);
    }, [])

    return (
        <>
            <div className="wrppr_cc03">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                </div>
                <div className="grd_cont">
                    <div className="rss9__10">
                        <div className="grd_rw1">
                            <div>
                                <Congr className="jello_vertical" />
                            </div>
                            
                            </div>
                        <div className="grd_rw2">
                            <h1>Congratulations</h1>
                            <h2>{terms[`RSS.Quiz.Score.${score<2?'2.Low':'1.High'}`].replace('%X', score).replace('%Y', records.length)}</h2>
                            <h3>{terms['RSS.Quiz.ScoreHigh']}</h3>
                        </div>
                        <div className="grd_rw3">
                            {/* <Btn text_btn={'Button'} /> <Btn text_btn={'Button'} /> <Btn text_btn={'Button'} /> */}
                            {nextActions}
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Rss2"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            {/* <Link to={"#"}>
                                <Btn text_btn={terms['core.Next']} />
                            </Link> */}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Rss9__10;