import {React,useState} from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"
import Family from "./Family"

import { ReactComponent as Cattles }    from '../assets/images/goals/cattles.svg'
import { ReactComponent as Crops }      from '../assets/images/goals/crops.svg'
import { ReactComponent as Farming }    from '../assets/images/goals/farming.svg'
import { ReactComponent as Goats }      from '../assets/images/goals/goats.svg'
import { ReactComponent as House }      from '../assets/images/goals/house.svg'
import { ReactComponent as Motorbike }  from '../assets/images/goals/motorbike.svg'
import { ReactComponent as Phone }      from '../assets/images/goals/phone.svg'
import { ReactComponent as Stationary } from '../assets/images/goals/stationary.svg'
import { ReactComponent as Tractor }    from '../assets/images/goals/tractor.svg'
import { ReactComponent as Tuktuk }      from '../assets/images/goals/tuktuk.svg'
import { ReactComponent as Plus }      from '../assets/images/icon-plus.svg'
import { Droppable } from "react-beautiful-dnd"
import { DragDropContext } from "react-beautiful-dnd"
import { Draggable } from "react-beautiful-dnd"

const Ss7 = ({terms,appData,setAppData}) => {

    console.log(appData)

    const icons = {
        'Cattles' : <Cattles/>,
        'Crops' : <Crops/>,
        'Farming' : <Farming/>,
        'Goats' : <Goats/>,
        'House' : <House/>,
        'Motorbike' : <Motorbike/>,
        'Phone' : <Phone/>,
        'Stationary' : <Stationary/>,
        'Tractor' : <Tractor/>,
        'Tuktuk' : <Tuktuk/>,
        'Plus': <Plus/>
    }

    const Goal = (data,container,key=0) => {

        let cssClass="goalItem selected-goal"
        if(data.type==='custom') cssClass+=" customGoal"

        return (
            <Draggable draggableId={`goal_${container}_${key}`} key={key} index={key}>
                {(provided,snapshot)=>(
                    <div {...provided.draggableProps} ref={provided.innerRef} {...provided.dragHandleProps} className={cssClass} >
                        { data.type==='fixed' && <div className="goal-icon  rotate_in_ver" >{icons[data.icon]}</div> }
                        { data.type==='custom' && <span>{data.name}</span> }
                    </div>
                )}
            </Draggable>
        )
    }

    const [goals,setGoals] = useState(appData.goalsTimeFrames ? [] : [...appData.goals])
    const [goalHigh,setGoalHigh] = useState(appData.goalsTimeFrames 
        ? {goal: appData.goals[appData.goalsTimeFrames.high.index], time:appData.goalsTimeFrames.high.time} 
        : {goal:undefined,time:undefined}
    )
    const [goalMedium,setGoalMedium] = useState(appData.goalsTimeFrames 
        ? {goal: appData.goals[appData.goalsTimeFrames.medium.index], time:appData.goalsTimeFrames.medium.time} 
        : {goal:undefined,time:undefined}
    )
    const [goalLow,setGoalLow] = useState(appData.goalsTimeFrames 
        ? {goal: appData.goals[appData.goalsTimeFrames.low.index], time:appData.goalsTimeFrames.low.time} 
        : {goal:undefined,time:undefined}
    )

    const saveData = () => {
        console.table([goalHigh,goalMedium,goalLow])
        let high = { 
            index: appData.goals.findIndex(g => g.name === goalHigh.goal.name), 
            time:goalHigh.time
        }
        let medium = { 
            index: appData.goals.findIndex(g => g.name === goalMedium.goal.name), 
            time:goalMedium.time
        }
        let low = { 
            index: appData.goals.findIndex(g => g.name === goalLow.goal.name), 
            time:goalLow.time
        }

        appData.goalsTimeFrames = {high,medium,low}
        setAppData(appData)
    }

    const onDragEnd = result => {

        if (!result.destination){
            //Possible combine
        }else{
            if(result.source.droppableId==='goals' && result.destination.droppableId!=='goals'){  // FROM GOALS TO TIME SLOT
                let newList = (result.destination.droppableId==='goalHigh'?goalHigh:(result.destination.droppableId==='goalMedium'?goalMedium:goalLow))
                let item = goals.splice(result.source.index,1)[0]           // Get dragged item
                let oldItem = newList.goal                                  // Get current item if exists
                newList.goal = item                                         // Insert item in slot
                if(oldItem) goals.splice(result.source.index,0,oldItem)     // Insert back previous item
            }else if(result.source.droppableId==='goals' && result.destination.droppableId==='goals'){  //Re order goals

            }else if(result.source.droppableId!=='goals' && result.destination.droppableId!=='goals'){
                let destination = (result.destination.droppableId==='goalHigh'?goalHigh:(result.destination.droppableId==='goalMedium'?goalMedium:goalLow))
                let source = (result.source.droppableId==='goalHigh'?goalHigh:(result.source.droppableId==='goalMedium'?goalMedium:goalLow))
                
                let item = source.goal                                  // Get dragged item
                let replacedItem = destination.goal                     // Get current item if exists
                destination.goal = item                                 // Insert item in slot
                source.goal = replacedItem                              // Insert back previous item
            }else{
                let source = (result.source.droppableId==='goalHigh'?goalHigh:(result.source.droppableId==='goalMedium'?goalMedium:goalLow))
                let item = source.goal                                      // Get dragged item
                goals.splice(result.destination.index,0,item)
                source.goal = undefined                                       // Insert item in slot
            }
        }

        let highTime = goalHigh.goal?.estimation
        let mediumTime = goalHigh.goal?.estimation + goalMedium.goal?.estimation
        let lowTime = goalHigh.goal?.estimation + goalMedium.goal?.estimation + goalLow.goal?.estimation

        goalHigh.time = !isNaN(highTime) ? getTimeLabel(highTime) : undefined
        goalMedium.time = !isNaN(mediumTime) ? getTimeLabel(mediumTime) : undefined
        goalLow.time = !isNaN(lowTime) ? getTimeLabel(lowTime) : undefined

        setGoalHigh({...goalHigh})
        setGoalMedium({...goalMedium})
        setGoalLow({...goalLow})
        setGoals([...goals])
    }



    const getTimeLabel = (estimation) => estimation < 12 ? `${Math.round(estimation*10)/10} ${terms['core.Months']}` : `${Math.round((estimation/12)*10)/10} ${terms['core.Years']}`

    return (
        <>
            <div className="cur_cc05cc01">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp22" style={{height:'', gridGap:'0px', gap:'0px' }}>
                        <DragDropContext onDragEnd={onDragEnd} >
                            <div className="grd_left">
                                    <div className="slide_in_blurred_left">
                                        <Family lady={appData.clientDetails.ladyAvatar} male={appData.clientDetails.isMarried==='Y'?appData.husband.husbandAvatar:false} children={appData.clientDetails.children} />
                                    </div>
                            </div>
                            <div className="grd_right">
                                <div className="">
                                    <h1>{terms['SS.FamilyPlan.Goals']}</h1>
                                    <Droppable droppableId={"goals"} type="goal" direction="horizontal">
                                        {(provided,snapshot)=>(
                                            <div {...provided.droppableProps} ref={provided.innerRef} className="family-goals" style={{display:'flex', justifyContent:'space-around', margin:'2rem 0rem'}}>
                                                { goals.map((goal,key) => Goal(goal,'goals',key)) }
                                                {provided.placeholder}
                                            </div>
                                        )}
                                    </Droppable>
                                </div>
                                <div className="goals-timeframe">
                                    <h3>{terms['SS.FamilyPlan.Timeframe']}</h3>
                                    <div id="flowBoxes">
                                        
                                        
                                    </div>
                                    <div className="time-slots">
                                        
                                        <div>
                                            <Droppable droppableId={"goalHigh"} type="goal" /* isDropDisabled={!!goalHigh.goal}*/ /* direction="horizontal" */ >
                                                {(provided,snapshot)=>(
                                                    <div {...provided.droppableProps} ref={provided.innerRef} className="time-box center-content">
                                                        { goalHigh.goal && Goal(goalHigh.goal,'high') }
                                                        {provided.placeholder}
                                                    </div>
                                                )}
                                            </Droppable>
                                            <h4>{goalHigh.time || '...'}</h4>
                                            
                                        </div>
                                        {goalMedium.goal &&
                                            <div className="flowboxes">
                                                {getTimeLabel(goalMedium.goal.estimation)}
                                            </div>
                                        }
                                        <div>
                                            <Droppable droppableId={"goalMedium"} type="goal" /* isDropDisabled={!!goalMedium.goal} */ /* direction="horizontal" */>
                                                {(provided,snapshot)=>(
                                                    <div {...provided.droppableProps} ref={provided.innerRef} className="time-box center-content">
                                                        { goalMedium.goal && Goal(goalMedium.goal,'medium') }
                                                        {provided.placeholder}
                                                    </div>
                                                )}
                                            </Droppable>
                                            <h4>{goalMedium.time || '...'}</h4>
                                            
                                        </div>
                                        {goalLow.goal &&
                                            <div className="flowboxes">
                                                {getTimeLabel(goalLow.goal.estimation)}
                                            </div>
                                        }
                                        <div>
                                            <Droppable droppableId={"goalLow"} type="goal" /* isDropDisabled={!!goalLow.goal} */ /* direction="horizontal" */>
                                                {(provided,snapshot)=>(
                                                    <div {...provided.droppableProps} ref={provided.innerRef} className="time-box center-content">
                                                        { goalLow.goal && Goal(goalLow.goal,'low') }
                                                        {provided.placeholder}
                                                    </div>
                                                )}
                                            </Droppable>
                                            <h4>{goalLow.time || '...'}</h4>
                                            
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </DragDropContext>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Ss6"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="280px"
                                    height="48px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right">
                            {goalHigh.goal && goalMedium.goal && goalLow.goal && 
                                <Link to={"/Ss8"}>
                                    <Btn text_btn={terms['core.Next']} click={()=>saveData()} />
                                </Link>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Ss7;