import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import { ReactComponent as Condoms } from '../assets/images/family_planning_methods/method_05.svg'
import { ReactComponent as EC } from '../assets/images/family_planning_methods/method_01.svg'
import { ReactComponent as Pills } from '../assets/images/family_planning_methods/method_04.svg'
import { ReactComponent as Injectables } from '../assets/images/family_planning_methods/method_06.svg'
import { ReactComponent as Implant } from '../assets/images/family_planning_methods/method_02.svg'
import { ReactComponent as IUD } from '../assets/images/family_planning_methods/method_03.svg'
import RadioButtons from '../components/btns/radioButton'
import { Link } from "react-router-dom"

const Fp32 = ({ terms, appData, setAppData , c4cMethods}) => {

    const [resultMethods, setResultMethods] = useState(appData.C4C.results)
    const suggestedMethods = appData.C4C.readyToChoose.screen === 1? resultMethods : c4cMethods
    const [selectedMethod, setSelectedMethod] = useState(appData.C4C.selectedMethod || '')

    const saveData = () => {
        appData.C4C.selectedMethod = selectedMethod
        setAppData(appData)
    }

    const methodMapping = (method) => {
        switch (method) {
            case 'IUD':
                return <IUD onClick={() => setSelectedMethod(method)} />
            case 'Implant':
                return <Implant onClick={() => setSelectedMethod(method)} />
            case 'Injectables':
                return <Injectables onClick={() => setSelectedMethod(method)} />
            case 'Pills':
                return <Pills onClick={() => setSelectedMethod(method)} />
            case 'EC':
                return <EC onClick={() => setSelectedMethod(method)} />
            case 'Condoms':
                return <Condoms onClick={() => setSelectedMethod(method)} />
            default:
                return <></>
        }
    }

    let results = suggestedMethods.map((method, key) =>
        <div className={"grd_c" + (key + 1)} key={key}>
            <div className="card_3">
                <div className={"avatar bk0" + (key + 2)}>{methodMapping(method)}</div>
                <div className="bttm">
                    <div className="r_bttons tracking_in_expand">
                        <RadioButtons checked={selectedMethod === method} click={() => setSelectedMethod(method)} name={'methods'} lbl={terms[`core.${method}`]} />
                    </div>
                </div>
            </div>
        </div>
    )

    return (
        <>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp32">
                        <div className="grd_line1">
                            <h1 className="slide_in_blurred_top">{terms['core.MethodInterest']}</h1>
                        </div>
                        <div className="grd_line2">
                            <div className="cont">
                                <div className="grd_cards">
                                    {results}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/c4c"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            <Link to={"/Fp33"}>
                                <Btn text_btn={terms['core.Next']} click={() => saveData()} />
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp32;