import React from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'

const Fp3 = ({terms,question,record,nextQuestion,returnQuestion,setQuestionsBlock,questions1}) => {

    return (
        <>
            <div className="wrppr_cc04">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp3">
                    <div className="grd_line1 ">
                    <h1 className="tracking_in_expand">{terms[record.score===1?'core.Correct':'core.Incorrect']}</h1>
                        </div>
                        <div className="grd_line2">
                            <div className="player-wrapper slide_in_blurred_bottom">
                                <ReactPlayer
                                    className="video_player"
                                    url={require('../assets/mp4/example_video.mp4')}
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            {/* <Link to={"/Fp2"}> */}
                                <Btn text_btn={terms['core.Back']} click={()=>returnQuestion(question)} />
                            {/* </Link> */}
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            {/* <Link to={"/Fp11_1"}> */}
                                <Btn text_btn={terms['core.Next']} click={()=>nextQuestion()} />
                            {/* </Link> */}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp3;