import React from "react"
import LogOut from '../components/btns/logOut'
import { ReactComponent as Book } from '../assets/images/icons/book.svg'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"


const Ss8_1_5 = ({ terms, appData, setAppData }) => {

    const setLastScreen = () => {
        appData.lastScreen = '/Ss8_1_5'
        setAppData(appData)
    }

    return (
        <>
            <div className="cur_cc05cc01">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="ss8_1_5">
                        <div className="grd_left">
                            <div className="jello_vertical"><Book /></div>

                        </div>
                        <div className="grd_right">
                            <h1 className="tracking_in_expand">{terms['SS.MethodLearn']}</h1>
                        </div>

                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Ss8_1"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="280px"
                                    height="48px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right">
                            <Link to={"/Fp1"}>
                                <Btn text_btn={terms['core.Next']} click={() => setLastScreen()} />
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Ss8_1_5;