import React from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import { ReactComponent as Condoms } from '../assets/images/family_planning_methods/method_05.svg'
import { ReactComponent as EC } from '../assets/images/family_planning_methods/method_01.svg'
import { ReactComponent as Pills } from '../assets/images/family_planning_methods/method_04.svg'
import { ReactComponent as Injectables } from '../assets/images/family_planning_methods/method_06.svg'
import { ReactComponent as Implant } from '../assets/images/family_planning_methods/method_02.svg'
import { ReactComponent as IUD } from '../assets/images/family_planning_methods/method_03.svg'

import { ReactComponent as Icon1 } from '../assets/images/icons/check_circle.svg'
import { ReactComponent as Icon2 } from '../assets/images/icons/arrows.svg'
import { ReactComponent as Icon3 } from '../assets/images/icons/error.svg'

import RadioButtons from '../components/btns/radioButton'
import { Link } from "react-router-dom"



const Fp33 = ({terms,appData,setAppData}) => {

    const method = appData.C4C.selectedMethod
    const imgMethod = {
        IUD:<IUD />,
        Implant:<Implant />,
        Injectables:<Injectables />,
        Pills:<Pills />,
        EC:<EC />,
        Condoms:<Condoms />,
    }[method]

    return (
        <>
            <div className="wrppr_cc02">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp33">
                        <div className="grd_line1 ">
                            <h1 className="slide_in_blurred_top">{terms['FP.MethodSelection.Congratulations1']}</h1>
                            <h2 className="slide_in_blurred_top">{terms['FP.MethodSelection.Congratulations2']}</h2>
                        </div>
                        <div className="grd_line2">
                            <div className="mssg">
                                <div className="grd_img">{imgMethod}</div>
                                <div className="grd_cont">
                                    <div className="grd_tile"><h2>{terms[`core.${method}`]}</h2></div>
                                    <div className="grd_c10">
                                        <Icon1 />
                                    </div>
                                    <div className="grd_c11">{terms[`MSG.${method}.Good`]}</div>
                                    <div className="grd_c20">
                                        <Icon2 />
                                    </div>
                                    <div className="grd_c21">{terms[`MSG.${method}.Ok`]}</div>
                                    <div className="grd_c30">
                                        <Icon3 />
                                    </div>
                                    <div className="grd_c31">{terms[`MSG.${method}.Bad`]}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Fp32"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            <Link to={"/Fp34"}>
                                <Btn text_btn={terms['core.Next']} />
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp33;