import React, { Children, useState } from "react";
import DatePicker from "react-datepicker";
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'

import { JsonToTable } from "react-json-to-table"

import { Link } from "react-router-dom"

const EndScreen = ({terms,appData,setAppData}) => {
    console.log(appData)
    
    let cuic = 
    (appData.clientDetails.firstName.slice(0,2)+
    appData.clientDetails.fatherName.slice(0,2)+
    (appData.husband.firstName?appData.husband.firstName.slice(0,2):'XX')+
    (appData.husband.fatherName?appData.husband.fatherName.slice(0,2):'XX')
    ).toUpperCase()+Math.floor(1000 + Math.random() * 9000);

    let ssResults = {
        cuic: cuic,
        sessionDetails: {
            gpsCoords: [0.0, 0.0], //lat, lon
            sessionDate: new Date(),
            sessionType: appData.clientDetails.sessionType
        },
        clientDetails:{
            firstName: appData.clientDetails.firstName,
            lastName: '', //Does not exist
            age: appData.clientDetails.age,
            fatherFirstName: appData.clientDetails.fatherName,
            region: appData.clientDetails.locations.region,
            zone: appData.clientDetails.locations.zone,
            district: appData.clientDetails.locations.woreda,
            kebele: appData.clientDetails.locations.kebele,
            childrenNumber: {babies: appData.clientDetails.children.numberBabies, boys: appData.clientDetails.children.numberBoys, girls: appData.clientDetails.children.numberGirls},
            referredBy: appData.clientDetails.whoReferred,
            currentMethod: appData.clientDetails.FPMethod,
            husbandInformation: {
                maritalStatus: '', // ??? Unable to determine
                husbandPresent: appData.clientDetails.byYourself==='byMyself'?'N':'Y',
                husbandFirstName: appData.husband?.firstName,
                husbandFatherFirstName: appData.husband?.fatherName
            },
            contactInformation: {
                phoneOwnership: appData.clientDetails.contactInformation.phoneOwnership,
                phoneNumber: appData.clientDetails.contactInformation.phoneNumber,
                consentServiceCalls: appData.clientDetails.contactInformation.interestedServiceCalls,
                consentFeedbackCalls: appData.clientDetails.contactInformation.interestedFeedbackCalls
            },
            c4cData:{
                currentlyPregnant: appData.C4C.answers.find(answer => answer.question === 'isPregnant').answer,
                methodDuringLastSex: appData.C4C.answers.find(answer => answer.question === 'lastSex').answer
            },
            referral: {
                issueReferral: appData.clientDetails.referredTo==='Neither'?'N':'Y',
                referredTo: appData.clientDetails.referredTo,
                helthPostName: appData.clientDetails.locations.kebele, //Same as Kebele selected
                appointmentDate: appData.clientDetails.appointmentDate,
                referralCode: ''
            }
        }
    }

    return (
        <>
            <div className="wrppr2">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="summaryTable">
                    <JsonToTable json={ssResults} />
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Fp45"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            {/* <Link to={"/Ds1"}>
                                <Btn text_btn={terms['core.Next']}  />
                            </Link> */}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default EndScreen;