import algorithmFull from '../assets/c4c/algorithm.json'
import questionsList from '../assets/c4c/questions.json'
import exceptions from '../assets/c4c/exceptions.json'

import Fp22 from "../pages/fp22"
import Fp23 from "../pages/fp23"
import Fp24 from "../pages/fp24"
import Fp25 from "../pages/fp25"
import Fp26 from "../pages/fp26"
import Fp27 from "../pages/fp27"
import Fp28 from "../pages/fp28"
import Fp29 from "../pages/fp29"

import { useState } from 'react'

const runAlgorithm = (algorithm,answersRecord,country) => {

    // Skip/Delete version key on algorithm
    if (algorithm.version) delete algorithm.version;

    var next_question, next_options;
    var factors = { "factor1": undefined, "factor2": undefined, "factor3": undefined };

    // Iterate user's answers
    for (let item of answersRecord) {    

        // >>> Store answers for factor 1,2 & 3
        if (factors[item.question]) factors[item.question]= item.answer

        // >>> Compare top-level algorithm with current answer
        var branches = Object.keys(algorithm).filter(branch => branch.includes(item.question) );
        if (branches.length > 0) {
            var nextBranch = branches.find(branch => branch.split(":")[1].includes(item.answer) );
            if (nextBranch) algorithm = algorithm[nextBranch];
        }
    }

    // >>> Get NEXT QUESTION and NEXT OPTIONS
    let algorithmKeys = Object.keys(algorithm);
    next_question = Array.isArray(algorithm) ? "results" : algorithmKeys[0].split(":")[0];
    if (next_question !== "results") {
        next_options = algorithmKeys.map(qa => qa.split(":")[1].replace(/\[|\]/g, "").split(",")).flat();
    }

    var question_data = questionsList[next_question];

    // >>> Populate options
    var options = {};
    if (next_question === "results") {

        // Filter methods based on user answers
        for (let e of Object.keys(exceptions.excludeAnswerBased)) {
            // Check if user's answer matches a exception
            let exceptionKeys = e.split(":");
            let exceptionQuestion = exceptionKeys[0];
            let exceptionAnswers = exceptionKeys[1].replace(/\[|\]/g, "").split(",").flat();

            let exceptionFound = answersRecord.find( item => item.question===exceptionQuestion && exceptionAnswers.includes(item.answer));//.length > 0;

            if (exceptionFound) {
                // Remove methods
                let methodsToRemove = exceptions.excludeAnswerBased[e].remove;
                algorithm = algorithm.filter(method => !methodsToRemove.includes(method));
                
                // Include new methods
                let methodsToAdd = exceptions.excludeAnswerBased[e].add;
                algorithm.concat(methodsToAdd);
                //methodsToAdd.forEach(method => algorithm.push(method));
                
                // Replace methods
                let methodsToReplace = exceptions.excludeAnswerBased[e].replace
                algorithm.map(currentMethod => {
                    let replacement = methodsToReplace.find(o => Object.keys(o)[0] === currentMethod);
                    return replacement ? Object.values(replacement)[0] : currentMethod;
                });
                /* methodsToReplace.forEach((replacement,algorithm) => {
                    var idx = algorithm.findIndex(method => method===Object.keys(replacement)[0]);
                    //var idx = algorithm.indexOf(Object.keys(replacement)[0]);
                    if (idx > -1) algorithm[idx] = Object.values(replacement)[0];
                }); */
            }
        };

        algorithm
            .filter(o => !(exceptions.excludeCountryBased[country] || []).includes(o))  // Filter exceptions by country
            //.slice(0, 2/*3*/)                                                                   // Limit to 2 methods
            .forEach(k => {                                                                 // Add filtered methods to options
                options[k] = question_data.options[k];
            });

    } else {
        [...Object.keys(question_data.options)].filter(o => next_options.includes(o)).forEach(k => {
            options[k] = question_data.options[k];
        });
    }
    // <<<

    // >>> Algorithm navigation result

    return {
        next_question,
        next_options,
        options
    };

}

const C4C = ({appData,setAppData,terms,C4CMethods}) => {

    // ------------------------------------------------------------------------- //
    const [age,setAge] = useState(appData.clientDetails.age < 18?'<18':'>18')       //If value doesn't exist, >18 is default
    const [gender,setGender] = useState(appData.clientDetails.gender || 'F')       //If value doesn't exist, F is default
    const [country,setCountry] = useState(appData.clientDetails.country || 'ET')   //If value doesn't exist, ET is default
    // ------------------------------------------------------------------------- //

    const [answers,setAnswers] = useState(appData.C4C.answers || [])
    const [previousAnswer,setPreviousAnswer] = useState([])
    
    const pushAnswers = (data)=> setAnswers(answers.concat(data)) 
    const deletePreviousAnswer = (count=1)=> {
        setPreviousAnswer(answers.splice(-count))
        setAnswers(answers/* .slice(0,-1) */)
    }
    const resetAnswers = () => setAnswers([])

    

    // -----------------------------DEBUGGING----------------------------------- //
    

    // ------------------------------------------------------------------------- //

    let algorithm = JSON.parse(JSON.stringify(algorithmFull));

    if(answers.length === 0){
        return <Fp22 terms={terms} pushAnswers={pushAnswers} gender={gender} age={age} previousAnswer={previousAnswer} appData={appData} />
    }else{
        let algorithmResults = runAlgorithm(algorithm,answers,country);
        
        switch(algorithmResults.next_question){
            case 'factor1':
                return <Fp23 appData={appData} terms={terms} options={algorithmResults.options} pushAnswers={pushAnswers} deletePreviousAnswer={deletePreviousAnswer} previousAnswer={previousAnswer}/>
            case 'factor2':
                return <Fp24 appData={appData} terms={terms} options={algorithmResults.options} pushAnswers={pushAnswers} deletePreviousAnswer={deletePreviousAnswer} previousAnswer={previousAnswer} />
            case 'factors2vs1':
                return <Fp25 terms={terms} options={algorithmResults.options} pushAnswers={pushAnswers} deletePreviousAnswer={deletePreviousAnswer} previousAnswer={previousAnswer} answers={answers} />
            case 'factor3':
                return <Fp26 appData={appData} terms={terms} options={algorithmResults.options} pushAnswers={pushAnswers} deletePreviousAnswer={deletePreviousAnswer} previousAnswer={previousAnswer} />
            case 'factors3vs12':
                return <Fp27 terms={terms} options={algorithmResults.options} pushAnswers={pushAnswers} deletePreviousAnswer={deletePreviousAnswer} previousAnswer={previousAnswer} answers={answers} />
            case 'sideEffects':
                return <Fp28 appData={appData} terms={terms} options={algorithmResults.options} pushAnswers={pushAnswers} deletePreviousAnswer={deletePreviousAnswer} previousAnswer={previousAnswer} />
            case 'results':
                //SAVE DATA
                
                // appData c4c { results, answers }
                let resultMethods = Object.keys(algorithmResults.options).map(res => res==='COP'?'Pills':res)
                appData.C4C.results  = resultMethods
                appData.C4C.answers = answers;
                setAppData(appData);

                return <Fp29 terms={terms} options={resultMethods} c4cMethods={C4CMethods} pushAnswers={pushAnswers} deletePreviousAnswer={deletePreviousAnswer} previousAnswer={previousAnswer} answers={answers} appData={appData} setAppData={setAppData} screen={1}/>
            default:
                resetAnswers();
                return <Fp22 appData={appData} pushAnswers={pushAnswers} gender={gender} age={age} />
        }
    }
}

export default C4C;
// ------------------------------------------------------------------------- //