import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'
import { useNavigate } from "react-router-dom"
import AnalyticsEventTracker from "./AnalyticsEventTracker";

const Int1 = ({ terms }) => {
    const [duration, setDuration] = useState(0);
    const [played, setPlayed] = useState(0);
    const navigate = useNavigate();

    const tracker = AnalyticsEventTracker('Welcome Video');

    const handlePlay = () => {
        tracker("Playing", "Welcome Song")
    }

    const handlePause = () => {
        tracker("Paused", "Welcome Song");
    }

    const handleProgress = state => {
        setPlayed(state.played)
    }

    const handleDuration = d => {
        setDuration(d)
    }

    const nextPage = () => {
        const elapsed_percentage = (((duration * played) / duration) * 100).toFixed(2);
        tracker("Elapsed", "Welcome Song", elapsed_percentage);
        navigate('/Int_2', { replace: true });
    }

    return (
        <>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top">
                        {/* <div className="select_lang slide_in_blurred_right" tabIndex="1">
                            <SelectOp ops={op_lngs.lngs} name={op_lngs.name} clss={op_lngs.clss} />
                        </div> */}
                    </div>
                </div>
                <div className="grd_cont">
                    <div className="int1">
                        <div className="grd_line1 ">
                            <h1 className="slide_in_blurred_top">{terms['INT.Welcome']}</h1>
                        </div>
                        <div className="grd_line2">
                            <div className="player-wrapper slide_in_blurred_bottom">
                                <ReactPlayer
                                    className="video_player"
                                    url={require('../assets/mp4/example_video.mp4')}
                                    playing={false}
                                    controls={true}
                                    autoPlay={false}
                                    onPlay={handlePlay}
                                    onPause={handlePause}
                                    onProgress={handleProgress}
                                    onDuration={handleDuration}
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right" onClick={nextPage} >
                            <Btn text_btn={terms['core.Next']} />
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Int1;