import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import RadioButtons from '../components/btns/radioButton'
import Family from "./Family"

const Fp24 = ({appData, terms, options, pushAnswers, deletePreviousAnswer, previousAnswer}) => {

    const [factor2,setFactor2] = useState(previousAnswer.find(a => a.question==='factor2')?.answer);

    return (
        <>
            <div className="cur_cc05cc01">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp24">
                        <div className="grd_left">
                            <Family lady={appData.clientDetails.ladyAvatar} husband={appData.husband?.husbandAvatar} children={appData.clientDetails.children} />
                        </div>
                        <div className="grd_right">
                            <div className="cont">
                            <h1 className="tracking_in_expand">{terms['c4c.preFactor2']}</h1>
                            <h2 className="tracking_in_expand">{terms['c4c.factor2']}</h2>
                                <div>

                                {   
                                options.EasyToStop &&
                                <div className="r_bttons">
                                    <RadioButtons checked={factor2==="EasyToStop"} click={()=>setFactor2('EasyToStop')} name={'factor2'} lbl={terms['c4c.options.EasyToStop']} />
                                </div>
                            }
                            {   
                                options.EasyToForget &&
                                <div className="r_bttons">
                                    <RadioButtons checked={factor2==="EasyToForget"} click={()=>setFactor2('EasyToForget')} name={'factor2'} lbl={terms['c4c.options.EasyToForget']} />
                                </div>
                            }
                            {   options.Effectiveness && 
                                <div className="r_bttons">
                                    <RadioButtons checked={factor2==="Effectiveness"} click={()=>setFactor2('Effectiveness')} name={'factor2'} lbl={terms['c4c.options.Effectiveness']} />
                                </div>
                            }
                            {   
                                options.Privacy &&
                                <div className="r_bttons">
                                    <RadioButtons checked={factor2==="Privacy"} click={()=>setFactor2('Privacy')} name={'factor2'} lbl={terms['c4c.options.Privacy']} />
                                </div>
                            }
                            {   
                                options.FewSideEffects &&
                                <div className="r_bttons">
                                    <RadioButtons checked={factor2==="FewSideEffects"} click={()=>setFactor2('FewSideEffects')} name={'factor2'} lbl={terms['c4c.options.FewSideEffects']} />
                                </div>
                            }
                            {   options.SelfAdministered && 
                                <div className="r_bttons">
                                    <RadioButtons checked={factor2==="SelfAdministered"} click={()=>setFactor2('SelfAdministered')} name={'factor2'} lbl={terms['c4c.options.SelfAdministered']} />
                                </div>
                            }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Btn text_btn={terms['core.Back']} click={()=>deletePreviousAnswer(1)}/>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            {factor2 && <Btn text_btn={terms['core.Next']} click={()=>pushAnswers({question:'factor2',answer:factor2})} />}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp24;