import React from "react"

export default function Quantity({ value, action, min, max, width, classes }) {
  return (
    <div className="quantity-input slide_in_blurred_bottom">
      <button className={"quantity-input__modifier quantity-input__modifier--left "+(classes|| '')} onClick={() => { if (value > (min || 0)) action(value - 1) }} >
        -
      </button>
      <input className={"quantity-input__screen "+(classes|| '')} type="text" value={value} readOnly style={width?{width:width+"%"}:undefined}/>
      <button className={"quantity-input__modifier quantity-input__modifier--right "+(classes|| '')} onClick={() => { if (!max || value < max) action( value + 1) }}>
        +
      </button>
    </div>
  );
}