import React from "react"
import InputMask from "react-input-mask"

export default function PhoneField(props) {

    return (
        <div className="input">
            <InputMask className="input_num" mask={"999 999 9999"} maskplaceholder="_" placeholder={props.holder} onChange={props.handleChange} value={props.currentValue}/>
            <label htmlFor="input" className="input_label">{props.flds_lbls}</label>
        </div>
    )
}