import React from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import { ReactComponent as NewUser } from '../assets/images/new_user.svg'
import { ReactComponent as ReturningUser } from '../assets/images/returning_user.svg'
import { Link } from "react-router-dom"
import AnalyticsEventTracker from "./AnalyticsEventTracker";

const int3 = ({ terms }) => {
    const tracker = AnalyticsEventTracker('New or Returning User?');
    return (
        <><div className="wrppr_cc04">
            <div className="bk_left"></div>
            <div className="bk_right"></div>
        </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="int3">

                        <div className="grd_line2">
                            <div className="cont">
                                <div className="grd_cards">
                                    <div className="grd_c1">
                                        <div className="card_1">
                                            <div className="avatar bk05 slide_in_blurred_left">
                                                <Link to={"/Ss1"} onClick={() => tracker('New')}>
                                                    <NewUser />
                                                </Link>
                                            </div>
                                            <div className="bttm tracking_in_expand">
                                                <h2 className={''/*"cc01"*/}>{terms['INT.New']}</h2>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="grd_c2">
                                        <div className="card_1">
                                            <div className="avatar bk07 slide_in_blurred_right">
                                                <Link to={"/Rss1"} onClick={() => tracker('Returning')}>
                                                    <ReturningUser />
                                                </Link>
                                            </div>
                                            <div className="bttm tracking_in_expand">
                                                <h2 className={'' /* "cc01" */}>{terms['INT.Returning']}</h2>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Int_2"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right"></div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default int3;