const getTerms = (strings,language,fallbackLanguage="en") => {

    const exceptions = []

    let fallbackTerms = strings.languages.find(lng => lng.code===fallbackLanguage).terms
    let terms = strings.languages.find(lng => lng.code===language)?.terms
    
    if(language===fallbackLanguage || !terms) return fallbackTerms

    Object.keys(terms).forEach(term => {
        if(terms[term]==='' || exceptions.includes(term)) terms[term] = fallbackTerms[term] 
    })

    return terms
}

