import React from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import Quantity from '../components/btns/quantity'
import { ReactComponent as Kid01 } from '../assets/images/hero/kids/baby_circular.svg'
import { ReactComponent as Kid02 } from '../assets/images/hero/kids/boy_circular.svg'
import { ReactComponent as Kid03 } from '../assets/images/hero/kids/girl_circular.svg'
import { Link } from "react-router-dom"
import { useState } from "react"

const Ss1_4 = ({ appData, setAppData, terms }) => {

    const [numberBabies, setNumberBabies] = useState(appData.clientDetails.children?.numberBabies || 0);
    const [numberBoys, setNumberBoys] = useState(appData.clientDetails.children?.numberBoys || 0);
    const [numberGirls, setNumberGirls] = useState(appData.clientDetails.children?.numberGirls || 0);

    const saveData = () => {
        appData.clientDetails.children = { numberBabies, numberBoys, numberGirls }
        setAppData(appData)
    }

    return (
        <div className="lyts">
            <div className="grd_top_nav">
                <div className="grd_left_top">
                    <LogOut />
                </div>
                <div className="grd_right_top"></div>
            </div>
            <div className="grd_cont">
                <div className="ss1_4">
                    <div className="grd_line1 ">
                        <h1 className="slide_in_blurred_top">{terms['SS.Children']}</h1>
                    </div>
                    <div className="grd_line2">
                            <div className="cont_avatars">
                                <div className="grd_c1">
                                    <div className="card_2">
                                        <div className="avatar bk02 slide_in_blurred_left">
                                            <Kid01 />
                                        </div>
                                        <Quantity value={numberBabies} action={setNumberBabies} />
                                    </div>
                                </div>
                                <div className="grd_c2">
                                    <div className="card_2">
                                        <div className="avatar bk12 slide_in_blurred_bottom">
                                            <Kid02 />
                                        </div>
                                        <Quantity value={numberBoys} action={setNumberBoys} />
                                    </div>
                                </div>
                                <div className="grd_c3">
                                    <div className="card_2">
                                        <div className="avatar bk05 slide_in_blurred_right">
                                            <Kid03 />
                                        </div>
                                        <Quantity value={numberGirls} action={setNumberGirls} />
                                    </div>
                                </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="grd_bottom_nav">
                <div className="btm_nab_one">
                    <div className="grd_btm_left">
                        <Link to={appData.clientDetails.isMarried==='Y' ? "/Ss1_3" : "/Ss1_2"}>
                            <Btn text_btn={terms['core.Back']} />
                        </Link>
                    </div>
                    <div className="grd_btm_center">
                    </div>
                    <div className="grd_btm_right">
                        <Link to={"/Ss1_5"}>
                            <Btn text_btn={terms['core.Next']} click={() => saveData()} />
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Ss1_4;