import React, { useEffect, useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import { ReactComponent as ByMySelft } from '../assets/images/avatars/wife.svg'
import { ReactComponent as WithMyHusband } from '../assets/images/avatars/husband_and_wife.svg'
import { Link } from "react-router-dom"
import AnalyticsEventTracker from "./AnalyticsEventTracker";

const Int2 = ({ appData, setAppData, terms }) => {
    const tracker = AnalyticsEventTracker('Self or With Husband?');
    const [byYourself, setByYourself] = useState(appData.clientDetails.byYourself);

    useEffect(() => {
        appData.clientDetails.byYourself = byYourself
        setAppData(appData)
    }, [byYourself]);

    return (
        <div className="lyts">
            <div className="grd_top_nav">
                <div className="grd_left_top">
                    <LogOut />
                </div>
                <div className="grd_right_top"></div>
            </div>
            <div className="grd_cont">
                <div className="int2">
                    <div className="grd_line1 ">
                        <h1 className="slide_in_blurred_top">{terms['INT.SelfHusband']}</h1>
                    </div>
                    <div className="grd_line2">
                        <div className="cont">
                            <div className="grd_cards">
                                <div className="grd_c1">
                                    <div className="card_1">
                                        <div className="avatar bk07 slide_in_blurred_left">
                                            <ByMySelft onClick={() => { setByYourself('byMyself'); tracker('By Myself'); }} className={byYourself === 'byMyself' ? 'svgSelected' : ''} />
                                        </div>
                                        <div className="bttm tracking_in_expand">
                                            <h2>{terms['INT.Self']}</h2>
                                        </div>
                                    </div>
                                </div>
                                <div className="grd_c2">
                                    <div className="card_1">
                                        <div className="avatar bk03 slide_in_blurred_right">
                                            <WithMyHusband onClick={() => { setByYourself('withMyHusband'); tracker('With My Husband'); }} className={byYourself === 'withMyHusband' ? 'svgSelected' : ''} />
                                        </div>
                                        <div className="bttm tracking_in_expand">
                                            <h2>{terms['INT.Husband']}</h2>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="grd_bottom_nav">
                <div className="btm_nab_one">
                    <div className="grd_btm_left">
                        <Link to={"/"}>
                            <Btn text_btn={terms['core.Back']} />
                        </Link>
                    </div>
                    <div className="grd_btm_center"></div>
                    <div className="grd_btm_right">
                        <Link to={"/Int_3"}>
                            {byYourself && <Btn text_btn={terms['core.Next']} />}
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Int2;