import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import RadioButtons from '../components/btns/radioButton'
import Family from "./Family"

const Fp23 = ({ appData, terms, options, pushAnswers, deletePreviousAnswer, previousAnswer }) => {

    const [factor1, setFactor1] = useState(previousAnswer.find(a => a.question === 'factor1')?.answer);

    return (
        <>
            <div className="cur_cc05cc01">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp23">
                        <div className="grd_left">
                            <Family lady={appData.clientDetails.ladyAvatar} husband={appData.husband?.husbandAvatar} children={appData.clientDetails.children} />
                        </div>
                        <div className="grd_right">
                            <div className="cont">
                                <h1 className="tracking_in_expand">{terms['c4c.factor1']}</h1>
                                <div>

                                    {options.Effectiveness && <div className="r_bttons">
                                        <RadioButtons checked={factor1 === "Effectiveness"} click={() => setFactor1('Effectiveness')} name={'radio1'} lbl={terms['c4c.options.Effectiveness']} />
                                    </div>}
                                    {options.Privacy && <div className="r_bttons">
                                        <RadioButtons checked={factor1 === "Privacy"} click={() => setFactor1('Privacy')} name={'radio1'} lbl={terms['c4c.options.Privacy']} />
                                    </div>}
                                    {options.FewSideEffects && <div className="r_bttons">
                                        <RadioButtons checked={factor1 === "FewSideEffects"} click={() => setFactor1('FewSideEffects')} name={'radio1'} lbl={terms['c4c.options.FewSideEffects']} />
                                    </div>}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Btn text_btn={terms['core.Back']} click={() => deletePreviousAnswer(6)} />
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            {factor1 && <Btn text_btn={terms['core.Next']} click={() => pushAnswers({ question: 'factor1', answer: factor1 })} />}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp23;