import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import { ReactComponent as Lady01 } from '../assets/images/hero/female/lady_01.svg'
import { ReactComponent as Lady02 } from '../assets/images/hero/female/lady_02.svg'
import { ReactComponent as Lady03 } from '../assets/images/hero/female/lady_03.svg'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"
import AnalyticsEventTracker from "./AnalyticsEventTracker";

const Ss1 = ({ appData, setAppData, terms }) => {
    const tracker = AnalyticsEventTracker('Avatar Selection');
    const [ladyAvatar, setLadyAvatar] = useState(appData.clientDetails.ladyAvatar);

    const setAvatar = (avatar) => {
        appData.clientDetails.ladyAvatar = avatar
        setAppData(appData)
        setLadyAvatar(avatar)
    }

    return (
        <div className="lyts">
            <div className="grd_top_nav">
                <div className="grd_left_top">
                    <LogOut />
                </div>
                <div className="grd_right_top"></div>
            </div>
            <div className="grd_cont">
                <div className="ss1">
                    <div className="grd_line1 ">
                        <h1 className="slide_in_blurred_top">{terms['SS.IntroAvatar']}</h1>
                    </div>
                    <div className="grd_line2">
                        <h2 className="tracking_in_expand">{terms['SS.IntroAvatar.Description']}</h2>
                    </div>
                    <div className="grd_line3">
                        <div className="cont_avatars">

                                <div className="a1">
                                    <div className="slide_in_blurred_left">
                                        <Link to={"/Ss1_1"} onClick={() => tracker('Lady 01')}>
                                            <Lady01 className={ladyAvatar === 'Lady01' ? 'svgSelected' : ''} onClick={() => setAvatar('Lady01')} />
                                        </Link>
                                    </div>
                                </div>
                                <div className="a2">
                                    <div className="slide_in_blurred_bottom">
                                        <Link to={"/Ss1_1"} onClick={() => tracker('Lady 03')}>
                                            <Lady03 className={ladyAvatar === 'Lady03' ? 'svgSelected' : ''} onClick={() => setAvatar('Lady03')} />
                                        </Link>
                                    </div>
                                </div>
                                <div className="a3">
                                    <div className="slide_in_blurred_right">
                                        <Link to={"/Ss1_1"} onClick={() => tracker('Lady 02')}>
                                            <Lady02 className={ladyAvatar === 'Lady02' ? 'svgSelected' : ''} onClick={() => setAvatar('Lady02')} />
                                        </Link>
                                    </div>
                                </div>

                        </div>
                    </div>
                </div>
            </div>
            <div className="grd_bottom_nav">
                <div className="btm_nab_one">
                    <div className="grd_btm_left">
                        <Link to={"/Int_3"}>
                            <Btn text_btn={terms['core.Back']} />
                        </Link>
                    </div>
                    <div className="grd_btm_center">
                        <div className="audioplayer">
                            <ReactPlayer
                                url={require('../assets/mp3/hothothot.mp3')}
                                width="280px"
                                height="48px"
                                playing={false}
                                controls={true}
                            />
                        </div>
                    </div>
                    <div className="grd_btm_right"></div>
                </div>
            </div>
        </div>
    )
}
export default Ss1;