import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import RadioButtons from '../components/btns/radioButton'
import ReactPlayer from 'react-player'
import Family from "./Family"

const Fp28 = ({ appData, terms, options, pushAnswers, deletePreviousAnswer, previousAnswer }) => {

    const [sideEffects, setSideEffects] = useState(previousAnswer.find(a => a.question === 'sideEffects')?.answer)

    return (
        <>
            <div className="cur_cc05cc01">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp28">
                        <div className="grd_left">
                            <Family lady={appData.clientDetails.ladyAvatar} husband={appData.husband?.husbandAvatar} children={appData.clientDetails.children} />
                        </div>
                        <div className="grd_right">
                            <h1 className="tracking_in_expand">{terms['c4c.FewSideEffects']}</h1>
                            <div className="grd_cta r_bttons">
                                <div>
                                    {options.PrevisiblePeriods && <div className="r_bttons">
                                        <RadioButtons checked={sideEffects === "PrevisiblePeriods"} click={() => setSideEffects('PrevisiblePeriods')} name={'sideEffects'} lbl={terms['c4c.options.PrevisiblePeriods']} />
                                    </div>}
                                    {options.NoNauseaNoHeadacheNoWeight && <div className="r_bttons">
                                        <RadioButtons checked={sideEffects === "NoNauseaNoHeadacheNoWeight"} click={() => setSideEffects('NoNauseaNoHeadacheNoWeight')} name={'sideEffects'} lbl={terms['c4c.options.NoNauseaNoHeadacheNoWeight']} />
                                    </div>}
                                    {options.LighterPeriods && <div className="r_bttons">
                                        <RadioButtons checked={sideEffects === "LighterPeriods"} click={() => setSideEffects('LighterPeriods')} name={'sideEffects'} lbl={terms['c4c.options.LighterPeriods']} />
                                    </div>}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Btn text_btn={terms['core.Back']} click={() => deletePreviousAnswer(1)} />
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="280px"
                                    height="48px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right">
                            {sideEffects && <Btn text_btn={terms['core.Next']} click={() => pushAnswers({ question: 'sideEffects', answer: sideEffects })} />}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp28;