import React from "react"

export default function NumField(props) {
    return (
        <div className="input">
            <input type="number" className="input_num" placeholder={props.flds_holder} min={props.min} max={props.max} onInput={props.handleChange} value={props.currentValue} pattern={props.pattern} />
            <label htmlFor="input" className="input_label">{props.flds_lbls}</label>
        </div>
    )
}