import React from "react"
import LogOut from '../components/btns/logOut'
import { ReactComponent as Done } from '../assets/images/icons/done.svg'
import { ReactComponent as Clear } from '../assets/images/icons/clear.svg'
import { Link } from "react-router-dom"
import Family from "./Family"

const Ss8_1_2 = ({terms,appData,setAppData}) => {

    const saveData = (methodSatisfaction) => {
        appData.clientDetails.methodSatisfaction = methodSatisfaction
        setAppData(appData)
    }

    return (
        <>
            <div className="cur_cc05cc01">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="ss8_1_2">
                        <div className="grd_left">
                            <Family lady={appData.clientDetails.ladyAvatar} husband={appData.clientDetails.isMarried === 'Y' ? appData.husband.husbandAvatar : false} children={appData.clientDetails.children} />
                        </div>
                        <div className="grd_right">
                            <div className="cont">
                                <h1 className="slide_in_blurred_left">{terms['core.MethodSatisfaction']}</h1>
                                <div className="btn_circle">
                                    <Link to={"/Ss8_1_3"}>
                                        <Done onClick={() => saveData('Y')} />
                                    </Link>
                                </div>
                                <div className="btn_circle">
                                    <Link to={"/Ss8_1_4"}>
                                        <Clear onClick={() => saveData('N')} />
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                        </div>
                        <div className="grd_btm_center">
                        </div>
                        <div className="grd_btm_right"></div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Ss8_1_2;