import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import { ReactComponent as Mobile1 } from '../assets/images/icons/mobile1.svg'
import { ReactComponent as Mobile2 } from '../assets/images/icons/mobile2.svg'
import { ReactComponent as Mobile3 } from '../assets/images/icons/mobile3.svg'
import RadioButtons from '../components/btns/radioButton'
import SelectOp from '../components/selectOp'
import PhoneField from '../components/phoneField'
import { Link } from "react-router-dom"

const Fp44 = ({terms,appData,setAppData}) => {

    const [phoneOwnership, setPhoneOwnership] = useState(appData.clientDetails.contactInformation?.phoneOwnership || '')
    const [phoneNumber, setPhoneNumber] = useState(appData.clientDetails.contactInformation?.phoneNumber || '')
    const [interestedServiceCalls, setInterestedServiceCalls] = useState(appData.clientDetails.contactInformation?.interestedServiceCalls || '')
    const [interestedFeedbackCalls, setInterestedFeedbackCalls] = useState(appData.clientDetails.contactInformation?.interestedFeedbackCalls || '')

    const handleChangePhoneOption = (e) => {
        let po = e.target.value
        setPhoneOwnership(po)
        if(e.target.value==='No' || e.target.value===''){
            setInterestedServiceCalls('')
            setInterestedFeedbackCalls('')
            setPhoneNumber('')
        }
    }

    const handleChangePhone = (e) => {
        setPhoneNumber(e.target.value)
    }

    let op_mobile = [
        {text: terms['Client.PhoneOwnership.No'], value: 'No'}, 
        {text: terms['Client.PhoneOwnership.Her'], value: 'Own'}, 
        {text: terms['Client.PhoneOwnership.Shared'], value: 'Shared'}
    ];

    const saveData = () => {
        appData.clientDetails.contactInformation = (phoneOwnership === 'No')
        ? { phoneOwnership } :
        appData.clientDetails.contactInformation = {
            phoneOwnership,
            phoneNumber,
            interestedServiceCalls,
            interestedFeedbackCalls,
        }
        
        setAppData(appData);
    }

    return (
        <>
            <div className="wrppr_cc02">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp44">
                        <div className="grd_line1 ">
                            {/* <h1 className="slide_in_blurred_top">{terms['Client.Closing.FollowUp']}</h1> */}
                        </div>
                        <div className="grd_line2">
                            <div className="mbl">
                                <div className="grd_icon rotate_in_ver">
                                    <Mobile1 />
                                </div>
                                <div className="grd_desc">{terms['Client.PhoneOwnership']}</div>
                                <div className="grd_cta">
                                    <div className="slide_in_blurred_right">
                                        <SelectOp ops={op_mobile} name={'phone'} currentValue={phoneOwnership} handleChange={(e)=>handleChangePhoneOption(e)} clss={'custom_select'} holder={terms['core.Select']} />
                                    </div>
                                    <div className="flds slide_in_blurred_right">
                                        { phoneOwnership && phoneOwnership!=='No' && 
                                            <PhoneField flds_lbls={terms['Client.PhoneNumber']} holder={terms['Client.PhoneNumber']} currentValue={phoneNumber} handleChange={(event)=>handleChangePhone(event)} />}
                                    </div>
                                </div>
                            </div>
                            { phoneOwnership && phoneOwnership!=='No' && 
                                <div className="mbl">
                                    <div className="grd_icon rotate_in_ver">
                                        <Mobile2 />
                                    </div>
                                    <div className="grd_desc">{terms['Client.ServiceConsent']}</div>
                                    <div className="grd_cta">
                                        <div className="r_bttons">
                                            <RadioButtons name={'radio1'} lbl={terms['core.Yes']} value={'Y'} checked={interestedServiceCalls==='Y'} click={()=> setInterestedServiceCalls('Y')} />
                                        </div>
                                        <div className="r_bttons">
                                            <RadioButtons name={'radio1'} lbl={terms['core.No']} value={'N'} checked={interestedServiceCalls==='N'} click={()=> setInterestedServiceCalls('N')} />
                                        </div>
                                    </div>
                                </div>}
                            { phoneOwnership && phoneOwnership!=='No' && 
                                <div className="mbl">
                                    <div className="grd_icon rotate_in_ver">
                                        <Mobile3 />
                                    </div>
                                    <div className="grd_desc">{terms['Client.FeedbackConsent']}</div>
                                    <div className="grd_cta">
                                        <div className="r_bttons">
                                            <RadioButtons name={'radio2'} lbl={terms['core.Yes']} value={'Y'} checked={interestedFeedbackCalls==='Y'} click={()=> setInterestedFeedbackCalls('Y')} />
                                        </div>
                                        <div className="r_bttons">
                                            <RadioButtons name={'radio2'} lbl={terms['core.No']} value={'N'} checked={interestedFeedbackCalls==='N'} click={()=> setInterestedFeedbackCalls('N')} />
                                        </div>
                                    </div>
                                </div>}
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/KQ"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            {phoneOwnership && (phoneOwnership==='No' || (phoneNumber.replace('_', '').trim().length === 12 && interestedServiceCalls!=='' && interestedFeedbackCalls!=='')) &&
                                <Link to={"/Fp45"}>
                                    <Btn text_btn={terms['core.Next']} click={()=> saveData()} />
                                </Link>}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp44;