import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import TextField from '../components/textField'
import { ReactComponent as Lady01 } from '../assets/images/hero/female/lady_01.svg'
import { ReactComponent as Male01 } from '../assets/images/hero/male/male_01.svg'

import { Link } from "react-router-dom"

const Rss1_1 = ({ appData, setAppData, resetData, terms }) => {

    /**
     * GET MINIMUM CLIENT & HUSBAND DETAILS (AVATARS,CHILDREN,)
     */
    const clientsDb = [
        {
            CUIC: "NYAJBITA004565",
            clientDetails: {
                ladyAvatar: "Lady01",
                firstName: "Nyala",
                age: "20",
                fatherName: "Ajani",
                whoReferred: "WDA",
                isMarried: "Y",
                children: {
                    numberBabies: 1,
                    numberBoys: 2,
                    numberGirls: 1
                },
                locations: {
                    region: "Oromiya",
                    zone: "Borena",
                    woreda: "Gomole",
                    kebele: "Surupa01"
                },
                sessionType: "individual"
            },
            husband: {
                husbandAvatar: "Male02",
                firstName: "Biruk",
                fatherName: "Tadele"
            }
        }
    ]

    const [firstName, setFirstName] = useState(appData.clientDetails.firstName || '')
    const [fatherName, setFatherName] = useState(appData.clientDetails.fatherName || '')
    const [husbandFirstName, setHusbandFirstName] = useState(appData.husband.firstName || '')
    const [husbandFatherName, setHusbandFatherName] = useState(appData.husband.fatherName || '')

    const handleChangeName = (e) => setFirstName(e.target.value)
    const handleChangeFatherName = (e) => setFatherName(e.target.value)
    const handleChangeHusbandName = (e) => setHusbandFirstName(e.target.value)
    const handleChangeHusbandFatherName = (e) => setHusbandFatherName(e.target.value)

    const [searchResults, setSearchResults] = useState(undefined)
    const [client, setClient] = useState(undefined)

    const stringMatch = (a, b) => {
        let longer = a.length >= b.length ? a : b;
        let shorter = longer === a ? b : a;

        return longer.indexOf(shorter) >= 0
    }

    const searchProfile = () => {
        resetData()
        let clients = clientsDb.filter(
            c =>
                stringMatch((firstName.slice(0, 2) + fatherName.slice(0, 2) + husbandFirstName.slice(0, 2) + husbandFatherName.slice(0, 2)).toLowerCase(), c.CUIC.toLocaleLowerCase())
            /*stringMatch(c.clientDetails.fatherName.toLowerCase(),fatherName.toLowerCase()) && 
            stringMatch(c.husband.firstName.toLowerCase(),husbandFirstName.toLowerCase()) && 
            stringMatch(c.husband.fatherName.toLowerCase(),husbandFatherName.toLowerCase())*/
        )
        if (clients.length > 0) setClient(clients[0])
        setSearchResults(clients)
    }

    const saveData = () => {
        appData.clientDetails.ladyAvatar = client.clientDetails.ladyAvatar

        appData.clientDetails.firstName = client.clientDetails.firstName
        appData.clientDetails.age = client.clientDetails.age
        appData.clientDetails.fatherName = client.clientDetails.fatherName
        appData.clientDetails.whoReferred = client.clientDetails.whoReferred

        appData.clientDetails.isMarried = client.clientDetails.isMarried

        appData.clientDetails.children = client.clientDetails.children

        appData.clientDetails.locations = client.clientDetails.locations

        appData.clientDetails.sessionType = client.clientDetails.sessionType

        appData.husband = client.husband

        setAppData(appData)
    }

    return (
        <div className="lyts">
            <div className="grd_top_nav">
                <div className="grd_left_top">
                    <LogOut />
                </div>
                <div className="grd_right_top"></div>
            </div>
            <div className="grd_cont">
                <div className="rss1">
                    <div className="grd_left">
                        <div className="slide_in_blurred_left">
                            <Lady01 />
                        </div>
                    </div>
                    <div className="grd_right">
                        <div className="cont">
                            <h1 className="slide_in_blurred_top">{terms['RSS.Search.PInfo']}</h1>
                            <div className=/* flds */" slide_in_blurred_right" style={{ width: '100% important', padding: '1em', float: 'none' }}>
                                <TextField flds_lbls={terms['RSS.Search.F2FN']} flds_holder={terms['RSS.Search.F2FN']} currentValue={firstName} handleChange={(event) => handleChangeName(event)} />
                            </div>
                            <div className=/* flds */" slide_in_blurred_right" style={{ width: '100% important', padding: '1em', float: 'none' }}>
                                <TextField flds_lbls={terms['RSS.Search.F2LN']} flds_holder={terms['RSS.Search.F2LN']} currentValue={fatherName} handleChange={(event) => handleChangeFatherName(event)} />
                            </div>
                            <div className=/* flds */" slide_in_blurred_right" style={{ width: '100% important', padding: '1em', float: 'none' }}>
                                <TextField flds_lbls={terms['RSS.Search.F2HFN']} flds_holder={terms['RSS.Search.F2HFN']} currentValue={husbandFirstName} handleChange={(event) => handleChangeHusbandName(event)} />
                            </div>
                            <div className=/* flds */" slide_in_blurred_right" style={{ width: '100% important', padding: '1em', float: 'none' }}>
                                <TextField flds_lbls={terms['RSS.Search.F2HLN']} flds_holder={terms['RSS.Search.F2HLN']} currentValue={husbandFatherName} handleChange={(event) => handleChangeHusbandFatherName(event)} />
                            </div>
                            {firstName && fatherName && <Btn text_btn={"Enter"} click={() => searchProfile()} />}
                        </div>
                    </div>
                </div>
            </div>
            {searchResults && client &&
                <div className="overlay">
                    <div className="overlay_cont slide_in_blurred_top">
                        <div className="cls_btn"><Btn text_btn="X" click={() => { setSearchResults(undefined); setClient(undefined) }} /></div>
                        <div className="cont_a">
                            <div className="avatars">
                                <div className="avatar_w">
                                    <div>
                                        <Lady01 />
                                        <div className="" style={{ textAlign: 'center' }}>
                                            {client.clientDetails.firstName}
                                        </div>
                                    </div>
                                </div>
                                <div className="avatar_h">
                                    <div>
                                        <Male01 />
                                        <div className="" style={{ textAlign: 'center' }}>
                                            {client.husband.firstName}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="grd_cta">
                            <Link to={"/Ss1"}>
                                <Btn text_btn={terms['RSS.Results.PInfoPopUp.2']} />
                            </Link>
                            <Link to={"/Rss2"}>
                                <Btn text_btn={terms['RSS.Results.PInfoPopUp.1']} click={() => saveData()} />
                            </Link>
                        </div>
                </div>
            }
            {searchResults && !client &&
                <div className="overlay">
                    <div className="lyts">
                        <div className="grd_top_nav">
                            <div className="grd_left_top"></div>
                            <div className="grd_right_top"><Btn text_btn="X" click={() => { setSearchResults(undefined); setClient(undefined) }} /></div>
                        </div>
                        <div className="grd_cont" >
                            <h2>No results found!</h2>
                        </div>
                    </div>
                </div>
            }
            <div className="grd_bottom_nav">
                <div className="btm_nab_one">
                    <div className="grd_btm_left">
                        <Link to={"/Int_3"}>
                            <Btn text_btn={terms['core.Back']} />
                        </Link>
                    </div>
                    <div className="grd_btm_center">
                    </div>
                    <div className="grd_btm_right">
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Rss1_1;