import React, { Component } from "react"

export default function TextField(props) {
    return (
        <div className="input">
            <input type="text" className="input_text" placeholder={props.flds_holder} onInput={props.handleChange} value={props.currentValue}/>
            <label htmlFor="input" className="input_label">{props.flds_lbls}</label>
        </div>
    )
}