import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'
import RadioButtons from '../components/btns/radioButton'
import { Link } from "react-router-dom"
import Family from "./Family"


const Rss2 = ({appData,setAppData,terms}) => {

    const [selectedOption,setSelectedOption] = useState(appData.clientDetails.lastEngage || '')

    const saveData = () => {
        appData.clientDetails.lastEngage = selectedOption
        setAppData(appData)
    }

    return (
        <>
            <div className="cur_cc05cc03">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="rss2">
                        <div className="grd_left">
                            <Family lady={appData.clientDetails.ladyAvatar} husband={appData.clientDetails.isMarried==='Y'?appData.husband.husbandAvatar:false} children={appData.clientDetails.children} />
                        </div>
                        <div className="grd_right">
                            <h1 className="tracking_in_expand">{terms['RSS.Welcome']}</h1>
                            <br/>
                            <h2 className="tracking_in_expand">{terms['RSS.EngageQ']}</h2>
                            <div className="r_bttons">
                                <RadioButtons name={'options'} lbl={terms['RSS.EngageQ.Opt1']} value={'2w'} checked={selectedOption==='2w'} click={()=>setSelectedOption('2w')} />
                                <RadioButtons name={'options'} lbl={terms['RSS.EngageQ.Opt2']} value={'1m'} checked={selectedOption==='1m'} click={()=>setSelectedOption('1m')} />
                                <RadioButtons name={'options'} lbl={terms['RSS.EngageQ.Opt3']} value={'1m2w'} checked={selectedOption==='1m2w'} click={()=>setSelectedOption('1m2w')} />
                                <RadioButtons name={'options'} lbl={terms['RSS.EngageQ.Opt4']} value={'>2m'} checked={selectedOption==='>2m'} click={()=>setSelectedOption('>2m')} />
                            </div>
                        </div>
                    </div>

                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Rss1"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="280px"
                                    height="48px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right">
                            {selectedOption && 
                                <Link to={"/RUQ"}>
                                    <Btn text_btn={terms['core.Next']} click={()=>saveData()} />
                                </Link>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Rss2;