import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'
import RadioButtons from '../components/btns/radioButton'
import Family from "./Family"

const Fp35 = ({appData, terms,question,addAnswer}) => {

    const [selectedOption,setSelectedOption] = useState('')

    const options = question.options.map((opt,key) => 
        <div className="r_bttons" key={key}>
            <RadioButtons name={'options'} lbl={terms[opt.term]} value={String(opt.value)} checked={selectedOption===String(opt.value)} click={()=>setSelectedOption(String(opt.value))} />
        </div>
    )

    return (
        <>
            <div className="cur_cc05cc03">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    
                    <div className={question.options.length < 3 ? "fp35" : "fp39"}>
                        <div style={{maxHeight:'500px'}}>{/* CAUTION */}
                            <Family lady={appData.clientDetails.ladyAvatar} husband={appData.husband?.husbandAvatar} children={appData.clientDetails.children} />
                        </div>
                        <div className="grd_right">
                            <h2 className="tracking_in_expand">{terms[question.term]}</h2>
                            { options }
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            {/*  <Link to={"/Fp34"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link> */}
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="280px"
                                    height="48px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right">
                            {selectedOption && 
                                <Btn text_btn={terms['core.Next']} click={()=>addAnswer(
                                    { question:question.term, answer:selectedOption, score:selectedOption===question.answer?1:0}
                                )} /> 
                            }
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp35;