import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import TextField from '../components/textField'
import { ReactComponent as Male01 } from '../assets/images/hero/male/male_01.svg'
import { ReactComponent as Male02 } from '../assets/images/hero/male/male_02.svg'
import { ReactComponent as Male03 } from '../assets/images/hero/male/male_03.svg'
import { Link } from "react-router-dom"

const Ss1_3 = ({ appData, setAppData, terms }) => {

    const [firstName, setFirstName] = useState(appData.husband.firstName || '');
    const [fatherName, setFatherName] = useState(appData.husband.fatherName || '');

    const handleChangeName = (e) => setFirstName(e.target.value);
    const handleChangeFatherName = (e) => setFatherName(e.target.value);

    const saveData = () => {
        appData.husband.firstName = firstName
        appData.husband.fatherName = fatherName
        setAppData(appData)
    }

    const avatars = {
        'Male01': <Male01 />,
        'Male02': <Male02 />,
        'Male03': <Male03 />,
    }
    const avatar = avatars[appData.husband.husbandAvatar]

    return (
        <div className="lyts">
            <div className="grd_top_nav">
                <div className="grd_left_top">
                    <LogOut />
                </div>
                <div className="grd_right_top"></div>
            </div>
            <div className="grd_cont">
                <div className="ss1_3">
                    <div className="grd_left">
                        <div className="slide_in_blurred_left">
                            {avatar}
                        </div>
                    </div>
                    <div className="grd_right">
                        <div className="cont">
                            <h1 className="slide_in_blurred_top">{terms['SS.HusbandInformation']}</h1>
                            <div className="form">
                                <div className="flds slide_in_blurred_right">
                                    <TextField flds_lbls={terms['core.FirstName']} flds_holder={terms['core.FirstName']} currentValue={firstName} handleChange={(event) => handleChangeName(event)} />
                                </div>
                                <div className="flds slide_in_blurred_right">
                                    <TextField flds_lbls={terms['core.FatherName']} flds_holder={terms['core.FatherName']} currentValue={fatherName} handleChange={(event) => handleChangeFatherName(event)} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="grd_bottom_nav">
                <div className="btm_nab_one">
                    <div className="grd_btm_left">
                        <Link to={"/Ss1_2"}>
                            <Btn text_btn={terms['core.Back']} />
                        </Link>
                    </div>
                    <div className="grd_btm_center">
                    </div>
                    <div className="grd_btm_right">
                        <Link to={"/Ss1_4"}>
                            {firstName.trim() && fatherName.trim() && <Btn text_btn={terms['core.Next']} click={() => saveData()} />}
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Ss1_3;