import React, { useState } from "react";
import DatePicker from "react-datepicker";
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import { ReactComponent as Mobile1 } from '../assets/images/icons/mobile1.svg'
import { ReactComponent as Mobile2 } from '../assets/images/icons/mobile2.svg'
import { ReactComponent as Mobile3 } from '../assets/images/icons/mobile3.svg'
import datepicker from "react-datepicker/dist/react-datepicker.css";
import RadioButtons from '../components/btns/radioButton'
import SelectOp from '../components/selectOp'
import NumField from '../components/numField'
import { Link } from "react-router-dom"

const Fp45 = ({terms,appData,setAppData}) => {

    const [appointmentDate, setAppointmentDate] = useState(appData.clientDetails.appointmentFullDate ? new Date(appData.clientDetails.appointmentFullDate) : new Date());

    const [referredTo,setReferredTo] = useState(appData.clientDetails.referredTo || '');

    const saveData = () => {
        appData.clientDetails.referredTo = referredTo
        appData.clientDetails.appointmentFullDate = appointmentDate.toDateString()
        appData.clientDetails.appointmentDate = appointmentDate.toISOString().split('T')[0]
        setAppData(appData)
    }

    return (
        <>
            <div className="wrppr_cc03">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp45">
                        <div className="cont">
                            <div className="grd_title1">
                                <h2 className="tracking_in_expand">{terms['FP.Referral']}</h2>
                            </div>
                            <div className="grd_title2">
                                <h2 className="tracking_in_expand">{terms['FP.Referral.Date']}</h2>
                            </div>
                            <div className="grd_r">
                                <div className="r_bttons tracking_in_expand">
                                    <RadioButtons checked={referredTo==='HEW'} value={'HEW'} click={()=>setReferredTo('HEW')} name={'referredTo'} lbl={terms['core.HEW']} />
                                </div>
                                <div className="r_bttons tracking_in_expand">
                                    <RadioButtons checked={referredTo==='MSI'} value={'MSI'} click={()=>setReferredTo('MSI')} name={'referredTo'} lbl={terms['core.MSI']} />
                                </div>
                                <div className="r_bttons tracking_in_expand">
                                    <RadioButtons checked={referredTo==='Neither'} value={'Neither'} click={()=>setReferredTo('Neither')} name={'referredTo'} lbl={terms['core.Neither']} />
                                </div>
                            </div>
                            <div className="grd_date">
                                <DatePicker
                                    selected={appointmentDate}
                                    onChange={(date) => setAppointmentDate(date)}
                                    monthsShown={1}
                                    inline
                                />
                            </div>
                        </div>

                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Fp44"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            <Link to={"/end"}>
                                <Btn text_btn={terms['core.Next']} click={()=>saveData()} />
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp45;