import {React,useState} from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"
import {DragDropContext,Droppable,Draggable} from "react-beautiful-dnd"
import NumField from '../components/numField'

import { ReactComponent as Cattles } from '../assets/images/goals/cattles.svg'
import { ReactComponent as Crops } from '../assets/images/goals/crops.svg'
import { ReactComponent as Farming } from '../assets/images/goals/farming.svg'
import { ReactComponent as Goats } from '../assets/images/goals/goats.svg'
import { ReactComponent as House } from '../assets/images/goals/house.svg'
import { ReactComponent as Motorbike } from '../assets/images/goals/motorbike.svg'
import { ReactComponent as Phone } from '../assets/images/goals/phone.svg'
import { ReactComponent as Stationary } from '../assets/images/goals/stationary.svg'
import { ReactComponent as Tractor } from '../assets/images/goals/tractor.svg'
import { ReactComponent as Tuktuk } from '../assets/images/goals/tuktuk.svg'
import { ReactComponent as Plus } from '../assets/images/icon-plus.svg'
import TextField from "../components/textField"

const Ss2 = ({ terms, appData, setAppData }) => {

    const goals = [
        { value: '1', name: terms['SS.GoalTitle.Cow'], term: 'SS.GoalTitle.Cow', costs: 15000, icon: 'Cattles', type: 'fixed' },
        { value: '2', name: terms['SS.GoalTitle.Shop'], term: 'SS.GoalTitle.Shop', costs: 12000, icon: 'Crops', type: 'fixed' },
        { value: '3', name: terms['SS.GoalTitle.Farm'], term: 'SS.GoalTitle.Farm', costs: 14500, icon: 'Farming', type: 'fixed' },
        { value: '4', name: terms['SS.GoalTitle.Goats'], term: 'SS.GoalTitle.Goats', costs: 18000, icon: 'Goats', type: 'fixed' },
        { value: '5', name: terms['SS.GoalTitle.House'], term: 'SS.GoalTitle.House', costs: 24000, icon: 'House', type: 'fixed' },
        { value: '6', name: terms['SS.GoalTitle.Motorbike'], term: 'SS.GoalTitle.Motorbike', costs: 15000, icon: 'Motorbike', type: 'fixed' },
        { value: '7', name: terms['SS.GoalTitle.Cell phone'], term: 'SS.GoalTitle.Cell phone', costs: 15500, icon: 'Phone', type: 'fixed' },
        { value: '8', name: terms['SS.GoalTitle.School'], term: 'SS.GoalTitle.School', costs: 12500, icon: 'Stationary', type: 'fixed' },
        { value: '9', name: terms['SS.GoalTitle.Tractor'], term: 'SS.GoalTitle.Tractor', costs: 17000, icon: 'Tractor', type: 'fixed' },
        { value: '10', name: terms['SS.GoalTitle.Bajaji'], term: 'SS.GoalTitle.Bajaji', costs: 10000, icon: 'Tuktuk', type: 'fixed' }
    ]

    const customGoal = { value: '', name: terms['SS.Goals.PopUp'], costs: 0, icon: 'Plus', type: 'custom' }

    const icons = {
        'Cattles': <Cattles />,
        'Crops': <Crops />,
        'Farming': <Farming />,
        'Goats': <Goats />,
        'House': <House />,
        'Motorbike': <Motorbike />,
        'Phone': <Phone />,
        'Stationary': <Stationary />,
        'Tractor': <Tractor />,
        'Tuktuk': <Tuktuk />,
        'Plus': <Plus />
    }

    const MAX_COLUMNS = 3
    const MAX_PER_COLUMN = Math.ceil(goals.length / MAX_COLUMNS)

    const [availableGoals, setAvailableGoals] = useState(
        goals
            .filter(goal => !appData.goals.find(savedGoal => savedGoal.value === goal.value))
            .concat(Array(3 - appData.goals.filter(goal => goal.type === 'custom').length).fill(undefined).map(i => ({ ...customGoal })))
            // push tantas custom como hagan falta
            .reduce((acu, cur, idx) => {
                (acu[idx % MAX_COLUMNS] ? acu[idx % MAX_COLUMNS].push(cur) : acu[idx % MAX_COLUMNS] = [cur]);
                return acu
            }, [])
    )
    const [chosenGoals, setChosenGoals] = useState(appData.goals.length > 0 ? appData.goals : [])

    const [showModal, setShowModal] = useState(false)
    const [formObserve, setFormObserve] = useState(undefined)
    const [formButtonStatus, setFormButtonStatus] = useState(false)

    const onDragStart = result => {
        /* console.log(result) */
    }

    const onDragEnd = result => {
        //Same or null droppable
        if (!result.destination) {
            if (result.combine) {

                const destinationIndex = parseInt(result.combine.draggableId.charAt(result.combine.draggableId.length - 1))
                const sourceColumn = parseInt(result.source.droppableId.charAt(result.source.droppableId.length - 1))

                let fromItem = availableGoals[sourceColumn].splice(result.source.index, 1)[0]
                let toItem = chosenGoals.splice(destinationIndex, 1)[0]

                chosenGoals.splice(destinationIndex, 0, fromItem)
                availableGoals[sourceColumn].splice(result.source.index, 0, toItem)

            } else return
        } else {
            if (result.destination.droppableId === 'chosenGoals') { // DROP IN CHOSEN GOALS
                if (chosenGoals.length === 3) return
                let item
                if (result.source.droppableId === 'chosenGoals') {  // FROM CHOSEN TO CHOSEN
                    item = chosenGoals.splice(result.source.index, 1)[0]
                } else {  //FROM AVAILABLE TO CHOSEN
                    const sourceColumn = parseInt(result.source.droppableId.charAt(result.source.droppableId.length - 1))
                    item = availableGoals[sourceColumn].splice(result.source.index, 1)[0]
                }
                chosenGoals.splice(result.destination.index, 0, item)

            } else {  // DROP IN AVAILABLE GOALS
                const destinationColumn = parseInt(result.destination.droppableId.charAt(result.destination.droppableId.length - 1))

                if (availableGoals[destinationColumn].length === MAX_PER_COLUMN && result.source.droppableId !== result.destination.droppableId) return;

                if (result.source.droppableId === 'chosenGoals') {  // FORM CHOSEN

                    let item = chosenGoals.splice(result.source.index, 1)[0]
                    availableGoals[destinationColumn].splice(result.destination.index, 0, item)
                } else {  // FROM AVAILABLE

                    const sourceColumn = parseInt(result.source.droppableId.charAt(result.source.droppableId.length - 1))

                    let item = availableGoals[sourceColumn].splice(result.source.index, 1)[0]
                    availableGoals[destinationColumn].splice(result.destination.index, 0, item)
                }
            }
        }

        setAvailableGoals([...availableGoals])
        setChosenGoals([...chosenGoals])

    }

    const prepareModal = (dataObj, list, index) => {
        setFormObserve({ data: { ...dataObj }, list: parseInt(list.charAt(list.length - 1)), index, default: dataObj.name })
        setShowModal(true)
    }

    const handleChangeGoal = (e) => {
        if (e.target.value !== terms['SS.Goals.PopUp'] && e.target.value !== '' && e.target.value.length <= 15) {
            formObserve.data.name = e.target.value
        }else{
            formObserve.data.name =  '';
        }

        setFormObserve({...formObserve})
        
        if(formObserve.data.costs !== 0 && formObserve.data.name !== ''){
            setFormButtonStatus(true)
        } else {
            setFormButtonStatus(false)
        }
    }

    const handleChangeCost = (e) => {
        if(e.target.value > 0){
            formObserve.data.costs = e.target.value
        }else{
            formObserve.data.costs = 0
        }

        setFormObserve({...formObserve})
        
        if(formObserve.data.costs !== 0 && formObserve.data.name !== ''){
            setFormButtonStatus(true)
        }else{
            setFormButtonStatus(false)
        }
    }

    const saveCustomGoal = () => {
        console.log(availableGoals, formObserve)
        let target = availableGoals[formObserve.list][formObserve.index]
        target.name = formObserve.data.name;
        target.costs = formObserve.data.costs;
        target.value = formObserve.data.name;
        setShowModal(false);
        setFormObserve(undefined);
        setAvailableGoals([...availableGoals])
        setChosenGoals([...chosenGoals])
        setFormButtonStatus(false)
        //console.log(goals)
    }

    const Goal = (data, container, key) => {
        //let goalDisabled = container.includes('available') && chosenGoals.length===3

        let cssClass = container === 'chosen' ? "goalItem selected-goal" : "goalItem"
        if (data.type === 'custom') cssClass += " customGoal"
        if (!data.value) cssClass += " cursor-pointer"


        return (
            <Draggable draggableId={`goal_${container}_${key}`} key={key} index={key} isDragDisabled={!data.value}>
                {(provided, snapshot) => (
                    <div
                        {...provided.draggableProps} ref={provided.innerRef}
                        {...provided.dragHandleProps}
                        className={cssClass}
                        onClick={data.type === 'custom' && container !== 'chosen' ? () => prepareModal(data, container, key) : undefined}
                    >
                        {!(data.type === 'custom' && container === 'chosen') &&
                            <div className="goal-icon rotate_in_ver" >{icons[data.icon]}</div>
                        }
                        {data.type === 'custom' && <span>{data.name}</span>}
                    </div>
                )}
            </Draggable>
        )
    }

    const GoalsGrid = (goals) => goals.map((list, listIndex) =>
        <Droppable droppableId={"availableGoals" + listIndex} type="goal" key={listIndex}  >
            {(provided, snapshot) => (
                <div {...provided.droppableProps} ref={provided.innerRef} className="goals-column">
                    {list.map((goal, key) => Goal(goal, 'available' + listIndex, key))}
                    {provided.placeholder}
                </div>
            )}
        </Droppable>

    )

    const saveData = () => {
        appData.goals = [...chosenGoals]
        setAppData(appData)
    }

    return (
        <>
            <div className="ff_cc02cc04">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">

                    <div className="ss2">
                        <DragDropContext onDragEnd={onDragEnd} onDragStart={onDragStart}>
                            <div className="grd_left grd-available-goals">
                                <div className="goals-title cc04"><h1 className="cc07">{terms['SS.Goals.Select']}</h1></div>
                                <div className="availableGoals">
                                    <div className="goals-column-layout">
                                        {GoalsGrid(availableGoals)}
                                    </div>
                                </div>
                            </div>
                            <div className="grd_right grd-selected-goals">
                                <div className="goals-title"><h1 className="cc02">{terms['SS.Goals.YourGoals']}</h1></div>
                                <div className="goals-selection">

                                    <Droppable droppableId="chosenGoals" type="goal" isCombineEnabled={true}>
                                        {(provided, snapshot) => (
                                            <div {...provided.droppableProps} ref={provided.innerRef} className="goals-row-layout goals-column rotate_in_ver_off">

                                                {chosenGoals && chosenGoals.map((goal, key) => Goal(goal, 'chosen', key))}
                                                {provided.placeholder}
                                            </div>
                                        )}
                                    </Droppable>
                                </div>
                            </div>
                        </DragDropContext>
                    </div>
                    {/*  <div className="ss2">
                        <h1>{Str.title}</h1>
                        <h2>{Str.sbtt}</h2>
                    </div> */}
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Ss1_6"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="400px"
                                    height="50px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right">
                            {chosenGoals.length === 3 &&
                                <Link to={"/Ss3"}>
                                    <Btn text_btn={terms['core.Next']} click={() => saveData()} />
                                </Link>
                            }
                        </div>
                    </div>
                </div>
            </div>
            {showModal &&
                <div className="overlay">
                    <div className="overlay_cont slide_in_blurred_top" style={{ width: '20%' }}>
                        <div className="cls_btn"><Btn text_btn="X" click={() => { setShowModal(false); setFormButtonStatus(false) }} /></div>
                        <div className="cont_a">
                            <div className="flds slide_in_blurred_right" style={{padding: '1rem 0'}}>
                                <TextField flds_lbls={terms['SS.Goals.PopUp']} flds_holder={formObserve.default} currentValue={formObserve.data.name!==terms['SS.Goals.PopUp']?formObserve.data.name:''} handleChange={(event) => handleChangeGoal(event)}/>
                            </div>
                            <div className={"flds slide_in_blurred_right "} style={{padding: '1rem 0 2rem'}}>
                                    <NumField flds_lbls={terms['core.Cost']} flds_holder={terms['core.Cost']} currentValue={formObserve.data.costs!==0?formObserve.data.costs:''} handleChange={(event) => handleChangeCost(event)} min={0}/>
                            </div>
                            {formButtonStatus && <Btn text_btn={terms['core.Enter']} click={()=>{saveCustomGoal()}} />}
                        </div>
                    </div>
                </div>
            }
        </>
    )
}
export default Ss2;