import React from "react"
import { ReactComponent as Lady01 } from '../assets/images/hero/female/lady_01.svg'
import { ReactComponent as Lady02 } from '../assets/images/hero/female/lady_02.svg'
import { ReactComponent as Lady03 } from '../assets/images/hero/female/lady_03.svg'
import { ReactComponent as Male01 } from '../assets/images/hero/male/male_01.svg'
import { ReactComponent as Male02 } from '../assets/images/hero/male/male_02.svg'
import { ReactComponent as Male03 } from '../assets/images/hero/male/male_03.svg'
import { ReactComponent as Kid01 } from '../assets/images/hero/kids/baby.svg'
import { ReactComponent as Kid02 } from '../assets/images/hero/kids/boy.svg'
import { ReactComponent as Kid03 } from '../assets/images/hero/kids/girl.svg'


const Family = ({ lady, husband, children }) => {

    const ladyAvatar = {
        'Lady01': <Lady01 />,
        'Lady02': <Lady02 />,
        'Lady03': <Lady03 />,
    }[lady]

    const husbandAvatar = {
        'Male01': <Male01 />,
        'Male02': <Male02 />,
        'Male03': <Male03 />,
    }[husband]


    let limBabies = children.numberBabies <= 3 ? children.numberBabies : 3;
    let limBoys = children.numberBoys <= 3 ? children.numberBoys : 3;
    let limGirls = children.numberGirls <= 3 ? children.numberGirls : 3;

    let babies = [];
    while (limBabies > 0) {
        babies.push(<div key={limBabies}><Kid01 /></div>);
        limBabies--;
    }

    let boys = [];
    while (limBoys > 0) {
        boys.push(<div key={limBoys}><Kid02 /></div>);
        limBoys--;
    }

    let girls = [];
    while (limGirls > 0) {
        girls.push(<div key={limGirls}><Kid03 /></div>);
        limGirls--;
    }

    return (
        <div className="cont">
            <div className="fathers">
                <div>{ladyAvatar}</div>
                <div>{husbandAvatar}</div>
            </div>
            <div className="cont_kids">
                    {boys}
                    {girls}
                    {babies}
            </div>
        </div>
    )

}
export default Family;