import React, { Component } from "react"

/**
 * props.ops -> <Array> [ {text,value} ]
 */

const SelectOp = (props) => {
    const options = props.ops

    return (
        <select id={props.name} name={props.name} className={props.clss} onChange={props.handleChange} value={props.currentValue}>
            <option key={0} value={''} >-- {props.holder} --</option>
            {options.map((opt, index) => {
                return <option key={index+1} value={opt.value} >{opt.text}</option>
            })}
        </select>
    )
}
export default SelectOp;