import React, { useEffect, useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import RadioButtons from '../components/btns/radioButton'
import { Link } from "react-router-dom"
import Family from "./Family"

const Fp22 = ({ appData, terms, pushAnswers, gender, age, previousAnswer }) => {

    const [isPregnant, setIsPregnant] = useState(previousAnswer.find(a => a.question === 'isPregnant')?.answer || '');
    const [lastSex, setLastSex] = useState(previousAnswer.find(a => a.question === 'lastSex')?.answer || '');

    const [data, setData] = useState([]);

    useEffect(() => {
        let currentAnswers = [];
        
        currentAnswers.push({ question: "gender", answer: gender });
        if (isPregnant){
            currentAnswers.push({ question: "isPregnant", answer: isPregnant });
            currentAnswers.push({ question: "futurePregnancy", answer: 'Y' });
            currentAnswers.push({ question: "breastFeeding", answer: 'N' });
            currentAnswers.push({ question: "age", answer: age });
        }
        if (lastSex) currentAnswers.push({ question: "lastSex", answer: lastSex });
        setData(currentAnswers);
    }, [isPregnant, lastSex])

    return (
        <>
            <div className="cur_cc05cc01">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp22">
                        <div className="grd_left">
                            <Family lady={appData.clientDetails.ladyAvatar} husband={appData.husband?.husbandAvatar} children={appData.clientDetails.children} />
                        </div>
                        <div className="grd_right">
                            <div className="cont">
                                <h1 className="tracking_in_expand">{terms['c4c.intro']}</h1>
                                <div>
                                    <h4>{terms['c4c.pregnant']}</h4>
                                    <div>
                                        <div className="r_bttons">
                                            <RadioButtons checked={isPregnant === "Y"} click={() => setIsPregnant('Y')} name={'pregnant'} lbl={terms['core.Yes']} />
                                        </div>
                                        <div className="r_bttons">
                                            <RadioButtons checked={isPregnant === "N"} click={() => setIsPregnant('N')} name={'pregnant'} lbl={terms['core.No']} />
                                        </div>
                                        <div className="r_bttons">
                                            <RadioButtons checked={isPregnant === "N/S"} click={() => setIsPregnant('N/S')} name={'pregnant'} lbl={terms['core.NotSure']} />
                                        </div>
                                    </div>
                                </div>
                                <div>
                                    <h4>{terms['c4c.lastSex']}</h4>
                                    <div>
                                        <div className="r_bttons">
                                            <RadioButtons checked={lastSex === "None"} click={() => setLastSex('None')} name={'lastSex'} lbl={terms['core.No']} />
                                        </div>
                                        <div className="r_bttons">
                                            <RadioButtons checked={lastSex === "EC"} click={() => setLastSex('EC')} name={'lastSex'} lbl={terms['core.Yes'] + ", " +terms['core.EC']} />
                                        </div>
                                        <div className="r_bttons">
                                            <RadioButtons checked={lastSex === "Condoms"} click={() => setLastSex('Condoms')} name={'lastSex'} lbl={terms['core.Yes'] + ", " +terms['core.Condoms']} />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Fp21"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            {data.length === 6 && <Btn text_btn={terms['core.Next']} click={() => pushAnswers(data)} />}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp22;