import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import { ReactComponent as Method5 } from '../assets/images/family_planning_methods/method_05.svg'
import { ReactComponent as Method1 } from '../assets/images/family_planning_methods/method_01.svg'
import { ReactComponent as Method4 } from '../assets/images/family_planning_methods/method_04.svg'
import { ReactComponent as Method6 } from '../assets/images/family_planning_methods/method_06.svg'
import { ReactComponent as Method2 } from '../assets/images/family_planning_methods/method_02.svg'
import { ReactComponent as Method3 } from '../assets/images/family_planning_methods/method_03.svg'
import { Link } from "react-router-dom"


const Ss8_1_1 = ({terms,appData,setAppData}) => {

    const [FPMethod,setFPMethod] = useState(appData.clientDetails.FPMethod)

    const saveData = () => {
        appData.clientDetails.FPMethod = FPMethod
        setAppData(appData)
    }

    return (
        <div className="lyts">
            <div className="grd_top_nav">
                <div className="grd_left_top">
                    <LogOut />
                </div>
                <div className="grd_right_top"></div>
            </div>
            <div className="grd_cont">
                <div className="ss8_1_1">
                    <div className="grd_line1">
                        <h1>{terms['core.MethodUsed']}</h1>
                    </div>
                    <div className="grd_line2">
                        <div className="grd_top">
                            <div className={"slide_in_blurred_left "+(FPMethod==='Condoms'?'selectedLight':'')}>
                                <Method5 onClick={()=>setFPMethod('Condoms')} className={''} />
                            </div>
                            <div className={"slide_in_blurred_right "+(FPMethod==='EC'?'selectedLight':'')}>
                                <Method1 onClick={()=>setFPMethod('EC')} className={''} />
                            </div>
                            <div className={"slide_in_blurred_right "+(FPMethod==='Pills'?'selectedLight':'')}>
                                <Method4 onClick={()=>setFPMethod('Pills')} className={''} />
                            </div>
                        </div>
                        <div className="grd_bttm">
                            <div className={"slide_in_blurred_left "+(FPMethod==='Injectables'?'selectedLight':'')}>
                                <Method6 onClick={()=>setFPMethod('Injectables')} className={''} />
                            </div>
                            <div className={"slide_in_blurred_right "+(FPMethod==='Implant'?'selectedLight':'')}>
                                <Method2 onClick={()=>setFPMethod('Implant')} className={''} />
                            </div>
                            <div className={"slide_in_blurred_right "+(FPMethod==='IUD'?'selectedLight':'')}>
                                <Method3 onClick={()=>setFPMethod('IUD')} className={''} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="grd_bottom_nav">
                <div className="btm_nab_one">
                    <div className="grd_btm_left">
                        <Link to={"/Ss8_1"}>
                            <Btn text_btn={terms['core.Back']} />
                        </Link>
                    </div>
                    <div className="grd_btm_center">
                    </div>
                    <div className="grd_btm_right">
                        <Link to={"/Ss8_1_2"}>
                            {FPMethod && <Btn text_btn={terms['core.Next']} click={()=>saveData()} />}
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Ss8_1_1;