import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"
import Quantity from '../components/btns/quantity'

const Ss5 = ({terms,appData,setAppData}) => {

    const [numberChilds, setNumberChilds] = useState(appData.goals.numberChilds || 0);
    const saveData = () => {
        appData.goals.numberChilds = numberChilds
    }

    return (
        <div className="lyts">
            <div className="grd_top_nav">
                <div className="grd_left_top">
                    <LogOut />
                </div>
                <div className="grd_right_top"></div>
            </div>
            <div className="grd_cont">
                <div className="ss5">
                    <div className="grd_line1 ">
                        <div className="cont slide_in_blurred_bottom"><h1>{terms['SS.BabyCalc.Cost']}</h1></div>
                    </div>
                    <div className="grd_line2">
                        
                    </div>
                    <div className="grd_line2">
                        <Quantity value={numberChilds} action={setNumberChilds} min={0} max={3} width={15} classes={"large-numbers"}/>
                    </div>
                </div>
            </div>
            <div className="grd_bottom_nav">
                <div className="btm_nab_one">
                    <div className="grd_btm_left">
                        <Link to={"/Ss4"}>
                            <Btn text_btn={terms['core.Back']} />
                        </Link>
                    </div>
                    <div className="grd_btm_center">
                        <div className="audioplayer">
                            <ReactPlayer
                                url={require('../assets/mp3/hothothot.mp3')}
                                width="280px"
                                height="48px"
                                playing={false}
                                controls={true}
                            />
                        </div>
                    </div>
                    <div className="grd_btm_right">
                        <Link to={"/Ss6"}>
                            <Btn text_btn={terms['core.Next']} click={()=>saveData()} />
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Ss5;