import React, { useEffect, useState } from "react"
import SelectOp from '../components/selectOp'
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import { Link } from "react-router-dom"
import locations from "../assets/locations/locations.json"

const Ss1_5 = ({appData,setAppData,terms}) => {

    const [regions,setRegions] = useState( locations.regions.map(r => ({text: r.region, value: r.code}) ) );
    const [zones,setZones] = useState([]);
    const [woredas,setWoredas] = useState([]);
    const [kebeles,setKebeles] = useState([]);

    const [region,setRegion] = useState(appData.clientDetails.locations?.region);
    const handleChangeRegion = (e) => {
        let region = e.target.value
        setRegion(region)

        setZone('')
        setZones( locations.zones.filter(z => z.region === region).map(z => ({text:z.zone, value: z.code})))
        
        setWoreda('')
        setWoredas([])

        setKebele('')
        setKebeles([])
    }

    const [zone,setZone] = useState(appData.clientDetails.locations?.zone);
    const handleChangeZone = (e) => {
        let zone = e.target.value
        
        setZone(zone)

        setWoreda('')
        setWoredas( locations.woredas.filter(w => w.zone === zone).map(w => ({text:w.woreda, value: w.code})))

        setKebele('')
        setKebeles([])
    }

    const [woreda,setWoreda] = useState(appData.clientDetails.locations?.woreda);
    const handleChangeWoreda = (e) => {
        let woreda = e.target.value
        setWoreda(woreda)
        setKebeles(
            locations.kebeles.filter(k => k.woreda === woreda).map(k => ({text:k.kebele, value: k.code}))
        )
    }

    const [kebele,setKebele] = useState(appData.clientDetails.locations?.kebele);
    const handleChangeKebele = (e) => {
        let kebele = e.target.value
        setKebele(kebele)
    }

    const [sessionType,setSessionType] = useState(appData.clientDetails.sessionType);

    const saveData = () => {
        appData.clientDetails.locations = {region:region,zone:zone,woreda:woreda,kebele:kebele}
        appData.clientDetails.sessionType = sessionType
        setAppData(appData)
    }

    useEffect(()=>{
        //First render
        if(region) handleChangeRegion({target:{value:region}})
        if(zone) handleChangeZone({target:{value:zone}})
        if(woreda) handleChangeWoreda({target:{value:woreda}})
        if(kebele) handleChangeKebele({target:{value:kebele}})
    },[])

    return (
        <div className="lyts">
            <div className="grd_top_nav">
                <div className="grd_left_top">
                    <LogOut />
                </div>
                <div className="grd_right_top"></div>
            </div>
            <div className="grd_cont">
                <div className="ss1_5">
                    <div className="grd_line1">
                        <h1 className="slide_in_blurred_top">{terms['Client.ChooseRegion']}</h1>
                    </div>
                    <div className="grd_line2">
                        <div className="slide_in_blurred_left">
                            <label htmlFor='regions'>{terms['Client.Region']}</label>
                            { regions && <SelectOp ops={regions} name={'regions'} clss={'custom_select'} currentValue={region} handleChange={(e)=>handleChangeRegion(e)} holder={terms['Client.Region']} /> }
                            
                        </div>
                        <div className="slide_in_blurred_left">
                        <label htmlFor='zones'>{terms['Client.Zone']}</label>
                            { zones && <SelectOp ops={zones} name={'zones'} clss={'custom_select'} currentValue={zone} handleChange={(e)=>handleChangeZone(e)} holder={terms['Client.Zone']} /> }
                        </div>
                        <div className="slide_in_blurred_right">
                            <label htmlFor='woredas'>{terms['Client.District']}</label>
                            { woredas && <SelectOp ops={woredas} name={'woredas'} clss={'custom_select'} currentValue={woreda} handleChange={(e)=>handleChangeWoreda(e)} holder={terms['Client.District']} /> }
                        </div>
                        <div className="slide_in_blurred_right">
                            <label htmlFor='kebele'>{terms['Client.Kebele']}</label>
                            { kebeles && <SelectOp ops={kebeles} name={'kebeles'} clss={'custom_select'} currentValue={kebele} handleChange={(e)=>handleChangeKebele(e)} holder={terms['Client.Kebele']} /> }
                        </div>
                    </div>
                    <div className="grd_line3">
                        <h2 className="tracking_in_expand">{terms['Client.SessionType']}</h2>
                    </div>
                    <div className="grd_line4">
                        <div className="slide_in_blurred_left">
                            <Btn text_btn={terms['Client.IndividualSession']} className={sessionType==='individual'?'btnSelected':''} click={()=>setSessionType('individual')} />
                        </div>
                        <div className="slide_in_blurred_right">
                            <Btn text_btn={terms['Client.GroupSession']} className={sessionType==='group'?'btnSelected':''} click={()=>setSessionType('group')} />
                        </div>
                    </div>
                </div>
            </div>
            <div className="grd_bottom_nav">
                <div className="btm_nab_one">
                    <div className="grd_btm_left">
                        <Link to={"/Ss1_4"}>
                            <Btn text_btn={terms['core.Back']} />
                        </Link>
                    </div>
                    <div className="grd_btm_center"></div>
                    <div className="grd_btm_right">
                        <Link to={"/Ss1_6"}>
                            {region && zone && woreda && kebele && sessionType && <Btn text_btn={terms['core.Next']} click={()=>saveData()} /> }
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Ss1_5;