import React from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"

const Fp43 = ({terms}) => {

    return (
        <>
            <div className="wrppr_cc05">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp43">
                        <div className="grd_title">
                            <h1>{terms['FP.Closing.1']}</h1>
                            {/*<h2>{Str.sbttl1}</h2>*/}
                        </div>
                        <div className="grd_results">
                            <div className="grd_left">
                                <div className="methods_f">
                                    <div className="video_m slide_in_blurred_left">
                                        <ReactPlayer
                                            url={require('../assets/mp4/example_video.mp4')}
                                            className="react-player"
                                            width="100%"
                                            height="100%"
                                            controls={true}
                                        />
                                    </div>
                                    <div>
                                        <p>{terms['FP.Closing.2']}</p>
                                    </div>
                                </div>
                            </div>
                            <div className="grd_right">
                                <div className="methods_f">
                                    <div className="video_m slide_in_blurred_left">
                                        <ReactPlayer
                                            url={require('../assets/mp4/example_video.mp4')}
                                            className="react-player"
                                            width="100%"
                                            height="100%"
                                            controls={true}
                                        />
                                    </div>
                                    <div>
                                        <p>{terms['FP.Closing.3']}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/KQ"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            <Link to={"/FP44"}>
                                <Btn text_btn={terms['core.Next']} />
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp43;