import { useEffect, useState } from "react";
import Fp2 from "./fp2";
import Fp3 from "./fp3";
import Fp11_1 from "./fp11_1";
import Fp20 from "./fp20";

/**
 *
 * @param {*} terms
 * @param {*} questions1 [ {term:MMBQ.Q1, answer:true }, ... ]
 * @param {*} records [{answer:'true',score:0},{answer:'false',score:1}, ...]
 * @param {*} functions {setMMBQBlock,setMMBQRecords}
 * @returns
 */
const MMBQ = ({ appData, setAppData, terms }) => {
    const [questions1,setQuestions1] = useState([]);
    const [questions2,setQuestions2] = useState([]);

    const [questionsBlock, setQuestionsBlock] = useState(1);
    const [question, setQuestion] = useState(undefined);
    const [lastRecord, setLastRecord] = useState(undefined);
    
    const [answerFeedback, setAnswerFeedback] = useState(false);
    const [currentRecords, setCurrentRecords] = useState([]);

    useEffect(() => {
        const MMBQBlock1 = terms["FP.MMBQ.Questions1"]
            .split(",")
            .map((question) => {
                let values = question.split("/");
                return { term: values[0], answer: values[1] };
            });
        const MMBQBlock2 = terms["FP.MMBQ.Questions2"]
            .split(",")
            .map((question) => {
                let values = question.split("/");
                return { term: values[0], answer: values[1] };
            });

        let q = MMBQBlock1.shift();
        setQuestion(q);

        setQuestions1(MMBQBlock1);
        setQuestions2(MMBQBlock2);

    }, []);

    const addAnswer = (data) => {
        setLastRecord(data);
        setCurrentRecords(currentRecords.concat(data));
        setAnswerFeedback(question);
    };

    const returnQuestion = (question) => {
        setQuestion(question);
        setAnswerFeedback(false);
    };

    const nextQuestion = () => {
        setQuestion(
            questions1.length > 0
                ? questions1.shift()
                : questionsBlock === 2
                    ? questions2.shift()
                    : undefined
        );
        setAnswerFeedback(false);
    };

    const clearBlock2 = () => {
        while (questions2.length > 0) questions2.shift();
        setAnswerFeedback(undefined);
    };

    if (question && !answerFeedback) {
        return <Fp2 terms={terms} appData={appData} question={question} addAnswer={addAnswer} />;
    } else if (question && answerFeedback) {
        return (
            <Fp3
                terms={terms}
                question={question}
                record={lastRecord}
                nextQuestion={nextQuestion}
                returnQuestion={returnQuestion}
            />
        );
    } else if (!question && questions1.length === 0 && questions2.length > 0) {
        return (
            <Fp11_1
                terms={terms}
                nextQuestion={nextQuestion}
                clearBlock2={clearBlock2}
                setQuestionsBlock={setQuestionsBlock}
            />
        );
    } else if (!question && questions1.length === 0 && questions2.length === 0) {
        return (
            <Fp20
                terms={terms}
                records={currentRecords}
                appData={appData}
                setAppData={setAppData}
            />
        );
    } else {
        return <></>;
    }
};

export default MMBQ;
