import React from "react"
import { ReactComponent as Logo } from './../../assets/images/logout.svg'

const SelectOp = () => {
    var isInIframe = (window.location !== window.parent.location);
    
    const goLogout = () => {
        if(isInIframe){
            window.parent.postMessage(JSON.stringify({ type: 'wfa-status', result: "logout" }), "*")
        }else if(window.location.pathname.includes('/jobs/')){
            window.location.href = '../../index1.html'
        }
    }

    return (
        <div className="btnLogOut slide_in_blurred_left" onClick={()=>goLogout()}>
            <Logo/>
        </div>
    )
}
export default SelectOp;