import React, { useEffect, useState } from "react"
import SelectOp from '../components/selectOp'
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import NumField from '../components/numField'

import { ReactComponent as Cattles } from '../assets/images/goals/cattles.svg'
import { ReactComponent as Crops } from '../assets/images/goals/crops.svg'
import { ReactComponent as Farming } from '../assets/images/goals/farming.svg'
import { ReactComponent as Goats } from '../assets/images/goals/goats.svg'
import { ReactComponent as House } from '../assets/images/goals/house.svg'
import { ReactComponent as Motorbike } from '../assets/images/goals/motorbike.svg'
import { ReactComponent as Phone } from '../assets/images/goals/phone.svg'
import { ReactComponent as Stationary } from '../assets/images/goals/stationary.svg'
import { ReactComponent as Tractor } from '../assets/images/goals/tractor.svg'
import { ReactComponent as Tuktuk } from '../assets/images/goals/tuktuk.svg'
import { ReactComponent as Plus } from '../assets/images/icon-plus.svg'

import { Link } from "react-router-dom"

const Ss3 = ({ terms, appData, setAppData }) => {

    const icons = {
        'Cattles': <Cattles />,
        'Crops': <Crops />,
        'Farming': <Farming />,
        'Goats': <Goats />,
        'House': <House />,
        'Motorbike': <Motorbike />,
        'Phone': <Phone />,
        'Stationary': <Stationary />,
        'Tractor': <Tractor />,
        'Tuktuk': <Tuktuk />,
        'Plus': <Plus />
    }

    const durationOptions = [   // Values is qtty of months
        { text: terms['core.SixMonths'], value: 6 },
        { text: terms['core.FirstYear'], value: 12 },
        { text: terms['core.TwoYears'], value: 24 },
        { text: terms['core.ThreeYears'], value: 36 },
        { text: terms['core.FourYears'], value: 48 },
        { text: terms['core.FiveYears'], value: 60 }
    ]

    const [showModal, setShowModal] = useState(false)

    const [goals, setGoals] = useState(appData.goals || [])
    const [currentGoal, setCurrentGoal] = useState(appData.goals[0])

    const [income, setIncome] = useState(currentGoal.income || '')
    const handleIncome = (e) => setIncome(e.target.value)

    const [expense, setExpense] = useState(currentGoal.expense || '')
    const handleExpense = (e) => setExpense(e.target.value)

    const [saving, setSaving] = useState(currentGoal.saving || '')
    const handleSaving = (e) => setSaving(e.target.value)

    const [duration, setDuration] = useState(currentGoal.duration || '')
    const handleDuration = (e) => setDuration(e.target.value)

    useEffect(() => {
        /* console.table(goals) */
    }, [goals])

    const getTimeLabel = months => months < 12 ?  `${months} ${terms['core.Months']}` : `${months/12} ${terms['core.Years']}`

    useEffect(() => {
        setIncome(currentGoal.income || '')
        setExpense(currentGoal.expense || '')
        setSaving(currentGoal.saving || '')
        setDuration(currentGoal.duration || '')
    }, [currentGoal])

    const calculateGoal = () => {
        currentGoal.income = income
        currentGoal.expense = expense
        currentGoal.saving = saving
        currentGoal.duration = duration

        if (income && expense && saving && duration) {
            let estimation = Math.ceil(currentGoal.costs / (income - expense) ) //Math.ceil(((currentGoal.costs / (income - expense)) / 12) * 10) / 10 //X.Y YEARS
            console.log(income, expense, saving, duration, estimation,currentGoal.costs / (income - expense))

            if (isFinite(estimation) && estimation >= 0) {
                currentGoal.estimation = estimation
                currentGoal.canAchive = estimation <= duration
                setShowModal(true)
            } else {
                delete currentGoal.estimation
                delete currentGoal.canAchive
            }
        } else {
            delete currentGoal.estimation
            delete currentGoal.canAchive
        }
        setGoals([...goals])
        //setCurrentGoal(currentGoal)
    }

    return (
        <div className="lyts">
            <div className="grd_top_nav">
                <div className="grd_left_top">
                    <LogOut />
                </div>
                <div className="grd_right_top"></div>
            </div>
            <div className="grd_cont">
                <div className="ss3">

                    <div className="grd_form">
                        <div className="cont">
                            <h1 className="slide_in_blurred_top">{currentGoal.term ? terms[currentGoal.term] : currentGoal.name} <br /> {terms['core.Cost']}: {currentGoal.costs}</h1>
                            <div className="flds slide_in_blurred_right">
                                <NumField flds_lbls={terms['SS.IncomeCalculator.Income']} flds_holder={terms['SS.IncomeCalculator.Income']} min={0} currentValue={income} handleChange={(e) => handleIncome(e)} />
                            </div>
                            <div className={"flds slide_in_blurred_right " + (Number.parseFloat(expense) + Number.parseFloat(saving) >= income ? "ageError" : "")}>
                                <NumField flds_lbls={terms['SS.IncomeCalculator.Expenses']} flds_holder={terms['SS.IncomeCalculator.Expenses']} min={0} currentValue={expense} handleChange={(e) => handleExpense(e)} />
                            </div>
                            <div className={"flds slide_in_blurred_right " + (Number.parseFloat(expense) + Number.parseFloat(saving) >= income ? "ageError" : "")}>
                                <NumField flds_lbls={terms['SS.IncomeCalculator.Savings.PopUp']} flds_holder={terms['SS.IncomeCalculator.Savings.PopUp']} min={0} currentValue={saving} handleChange={(e) => handleSaving(e)} />
                            </div>
                            <div className="flds slide_in_blurred_right">
                                <SelectOp ops={durationOptions} holder={terms['SS.IncomeCalculator.Duration']} name={'duration'} clss={'custom_select1'} currentValue={duration} handleChange={(e) => handleDuration(e)} />
                            </div>

                        </div>
                    </div>
                    <div className="grd_selected">
                        <div className="slide_in_blurred_left rotate_in_ver">
                            {currentGoal && icons[currentGoal.icon]}
                        </div></div>
                    <div className="grd_icons">
                        <div className="income-goals" style={{ display: 'flex' }}>
                            {goals.map((goal, key) => (
                                <div className="goal-icon center-content rotate_in_ver" onClick={() => setCurrentGoal(goal)} key={key}>
                                    {goal.type === 'fixed' && icons[goal.icon]}
                                    {goal.type === 'custom' && <span>{goal.name}</span>}
                                    {goal.estimation !== undefined && goal.canAchive && <div className="badge" >&#x2714;</div>}
                                    {goal.estimation !== undefined && !goal.canAchive && <div className="badge" >&#10008;</div>}
                                </div>
                            ))}

                        </div>
                    </div>
                    <div className="grd_cta">
                        <div className="btns">
                            <Btn text_btn={terms['SS.IncomeCalculator.Calculate']} click={() => calculateGoal()} />
                        </div>
                    </div>
                </div>
            </div>
            <div className="grd_bottom_nav">
                <div className="btm_nab_one">
                    <div className="grd_btm_left">
                        <Link to={"/Ss2"}>
                            <Btn text_btn={terms['core.Back']} />
                        </Link>
                    </div>
                    <div className="grd_btm_center">
                    </div>
                    <div className="grd_btm_right">
                        {goals.filter(g => g.canAchive).length === 3 &&
                            <Link to={"/Ss4"}>
                                <Btn text_btn={terms['core.Next']} />
                            </Link>
                        }
                    </div>
                </div>
            </div>
            {showModal &&
                <div className="overlay">
                    <div className="overlay_cont slide_in_blurred_top" style={{ width: '60%' }}>
                        <div className="cont_a center-content-column">
                            <h1>{terms['SS.IncomeCalculator.PopUp.GoalEst']}</h1>
                            <div className="goals-table">
                                <div className="multi-column">
                                    <div className="two-column goals-col">{terms['SS.IncomeCalculator.Income']}</div>
                                    <div className="two-column goals-col">{currentGoal.income}</div>
                                </div>
                                <div className="multi-column">
                                    <div className="two-column goals-col">{terms['SS.IncomeCalculator.Expenses']}</div>
                                    <div className="two-column goals-col">{currentGoal.expense}</div>
                                </div>
                                <div className="multi-column">
                                    <div className="two-column goals-col">{terms['SS.IncomeCalculator.Savings.PopUp']}</div>
                                    <div className="two-column goals-col">{currentGoal.saving}</div>
                                </div>
                                <div className="multi-column">
                                    <div className="two-column goals-col">{terms['SS.IncomeCalculator.Duration']}</div>
                                    <div className="two-column goals-col">{getTimeLabel(currentGoal.duration)}</div>
                                </div>
                            </div>

                            <div className="disclaimer">
                                {currentGoal.canAchive ? terms['SS.IncomeCalculator.PopUp.Great'] : terms['SS.IncomeCalculator.PopUp.Disclaimer']}
                            </div>

                            <Btn text_btn={terms['core.Done']} click={() => { setShowModal(false) }} />
                        </div>
                    </div>
                </div>
            }
        </div>
    )
}
export default Ss3;