import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd'


const Fp25 = ({ terms, options, pushAnswers, deletePreviousAnswer, previousAnswer, answers }) => {

    const [factors2vs1, setFactors2vs1] = useState(previousAnswer.find(a => a.question === 'factors2vs1')?.answer)
    const factor1 = answers.find(a => a.question === 'factor1')?.answer;
    const factor2 = answers.find(a => a.question === 'factor2')?.answer;

    const onDragEnd = (result) => {
        //Same or null droppable
        if (!result.destination || result.source.droppableId === result.destination.droppableId) return

        switch (result.destination.droppableId) {
            case 'more':
                setFactors2vs1('+')
                break
            case 'equal':
                setFactors2vs1('=')
                break
            case 'less':
                setFactors2vs1('-')
                break
            case 'from':
                setFactors2vs1(undefined)
                break
            default:
                break
        }
    }

    const renderFactor2 = (factor) => {
        return <Draggable draggableId="factor2" index={0}>
            {(provided, snapshot) => (
                <div className="factor-draggable" {...provided.draggableProps} ref={provided.innerRef} {...provided.dragHandleProps}>
                    <span>{terms[`c4c.options.${factor}`]}</span>
                </div>
            )}
        </Draggable>
    }

    return (
        <>
            <div className="ff_cc01cc05">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp25">

                        <div class="grd_top">
                            <h2 className="tracking_in_expand">
                                {
                                    terms['c4c.factor2vs1']
                                        .replace(/\{.*1\}*\}/g, terms[`c4c.options.${factor1}`])
                                        .replace(/\{.*2\}*\}/g, terms[`c4c.options.${factor2}`])
                                }
                            </h2>
                        </div>
                        <div class="grd_content">
                            <DragDropContext onDragEnd={onDragEnd}>
                                <Droppable droppableId="from" type="factor">
                                    {(provided, snapshot) => (
                                        <div {...provided.droppableProps} ref={provided.innerRef} className={"factor_wrapper droppableArea"}>
                                            {factors2vs1 === undefined && renderFactor2(factor2)}
                                            {provided.placeholder}
                                        </div>
                                    )}
                                </Droppable>
                                <div className="multi-column">
                                    <div className="two-column">
                                        {terms[`c4c.options.${factor1}`]}
                                    </div>
                                    <div className="two-column">
                                        {options['+'] &&
                                            <>
                                                <div className="drop_label">({terms['c4c.options.+']})</div>
                                                <Droppable droppableId="more" type="factor" className="factor">
                                                    {(provided, snapshot) => (
                                                        <div {...provided.droppableProps} ref={provided.innerRef} className={"factor_wrapper droppableArea"} >
                                                            {factors2vs1 === '+' && renderFactor2(factor2)}
                                                            {provided.placeholder}
                                                        </div>
                                                    )}
                                                </Droppable>
                                            </>
                                        }
                                        {options['='] &&
                                            <>
                                                <div className="drop_label">({terms['c4c.options.=']})</div>
                                                <Droppable droppableId="equal" type="factor" className="factor">
                                                    {(provided, snapshot) => (
                                                        <div {...provided.droppableProps} ref={provided.innerRef} className={"factor_wrapper droppableArea"} >

                                                            {factors2vs1 === '=' && renderFactor2(factor2)}
                                                            {provided.placeholder}
                                                        </div>
                                                    )}
                                                </Droppable>
                                            </>
                                        }
                                        {options['-'] &&
                                            <>
                                                <div className="drop_label">({terms['c4c.options.-']})</div>
                                                <Droppable droppableId="less" type="factor" className="factor">
                                                    {(provided, snapshot) => (
                                                        <div {...provided.droppableProps} ref={provided.innerRef} className={"factor_wrapper droppableArea"} >
                                                            {factors2vs1 === '-' && renderFactor2(factor2)}
                                                            {provided.placeholder}
                                                        </div>
                                                    )}
                                                </Droppable>
                                            </>
                                        }
                                    </div>
                                </div>
                            </DragDropContext>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Btn text_btn={terms['core.Back']} click={() => deletePreviousAnswer(1)} />
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            {factors2vs1 && <Btn text_btn={terms['core.Next']} click={() => pushAnswers({ question: 'factors2vs1', answer: factors2vs1 })} />}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp25;