import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'
import RadioButtons from '../components/btns/radioButton'
import { Link } from "react-router-dom"
import Family from "./Family"

const Fp2 = ({ appData, terms, question, addAnswer }) => {

    const [answerValue, setAnswerValue] = useState(undefined);

    return (
        <>
            <div className="cur_cc04">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp2">
                        <div className="grd_left">
                            <Family lady={appData.clientDetails.ladyAvatar} husband={appData.husband?.husbandAvatar} children={appData.clientDetails.children} />
                        </div>
                        <div className="grd_right">
                            <div className="cont">
                                <h2 className="tracking_in_expand">{terms[question.term]}</h2>
                                <div className="r_bttons">
                                    <RadioButtons name={'MMBQAnswer'} lbl={terms['core.True']} checked={answerValue === 'true'} click={() => setAnswerValue('true')} />
                                </div>
                                <div className="r_bttons">
                                    <RadioButtons name={'MMBQAnswer'} lbl={terms['core.False']} checked={answerValue === 'false'} click={() => setAnswerValue('false')} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Fp1"}>
                                <Btn text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="280px"
                                    height="48px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right">
                            {/* <Link to={"/Fp3"}> */}
                            {answerValue && <Btn text_btn={terms['core.Next']} click={() => addAnswer({ question: question.term, answer: answerValue, score: answerValue === question.answer ? 1 : 0 })} />}
                            {/* </Link> */}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp2;