import { React, useState } from "react"
import SelectOp from '../components/selectOp'
import LogOut from '../components/btns/logOut'
import Btn from '../components/btns/btn'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"
import { ReactComponent as Condoms } from '../assets/images/family_planning_methods/method_05.svg'
import { ReactComponent as EC } from '../assets/images/family_planning_methods/method_01.svg'
import { ReactComponent as Pills } from '../assets/images/family_planning_methods/method_04.svg'
import { ReactComponent as Injectables } from '../assets/images/family_planning_methods/method_06.svg'
import { ReactComponent as Implant } from '../assets/images/family_planning_methods/method_02.svg'
import { ReactComponent as IUD } from '../assets/images/family_planning_methods/method_03.svg'

import { ReactComponent as Icon1 } from '../assets/images/icons/check_circle.svg'
import { ReactComponent as Icon2 } from '../assets/images/icons/arrows.svg'
import { ReactComponent as Icon3 } from '../assets/images/icons/error.svg'

const Ss8_2_3 = ({appData, resetData, terms}) => {

    const [methodInfo, setMethodInfo] = useState(undefined)

    let op_am = {
        name: 'amethod',
        clss: 'custom_select',
        mthds: [
            {text:terms['core.IUD'],value:'IUD', component: <IUD />}, 
            {text:terms['core.Implant'],value:'Implant', component: <Implant />}, 
            {text:terms['core.Condoms'],value:'Condoms', component: <Condoms />}, 
            {text:terms['core.Injectables'],value:'Injectables', component: <Injectables />}, 
            {text:terms['core.Pills'],value:'Pills', component: <Pills />}, 
            {text:terms['core.EC'],value:'EC', component: <EC />}
        ]
    }

    const handleChangeMethod = (e) => {
        let method = e.target.value;
        let res = op_am.mthds.find(m=> m.value===method);
        setMethodInfo(res);
    }

    return (
        <>
            <div className="wrppr_cc05">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="ss8_2_3">
                        <div className="grd_line1 ">
                            <h1 className="slide_in_blurred_top">{terms['SS.MethodNoInterestThankYou.1']}</h1>
                            <h2 className="tracking_in_expand">{terms['SS.MethodNoInterest.ThankYou.2']}</h2>
                        </div>
                        <div className="grd_line2">
                            <div>
                                <div>
                                    <p className="slide_in_blurred_left">{terms['SS.MethodNoInterest.Summary']}</p>
                                </div>
                                <div className="slide_in_blurred_right">
                                    <SelectOp ops={op_am.mthds} handleChange={(e)=>handleChangeMethod(e)} name={op_am.name} clss={op_am.clss} holder={terms['core.Select']}/>
                                </div>
                            </div>
                        </div>
                        <div className="grd_line3">
                            <div className="content_video slide_in_blurred_bottom">
                                <ReactPlayer className="video_player"
                                    url={require('../assets/mp4/example_video.mp4')}
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">

                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            <Link to={"/"}>
                                <Btn text_btn={terms['core.Done']} click={()=>resetData()}/>
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
            { methodInfo &&
                <div className="overlay">
                <div className="overlay_cont slide_in_blurred_top" style={{width: '80%'}}>
                    <div className="cls_btn"><Btn text_btn="X" click={() => { setMethodInfo(undefined); }} /></div>
                    <div className="cont_a">
                        <div className="fp33">
                            <div className="grd_line2"> 
                                <div className="mssg">
                                    <div className="grd_img">{methodInfo.component}</div>
                                    <div className="grd_cont">
                                        <div className="grd_tile"><h2>{terms[`core.${methodInfo.value}`]}</h2></div>
                                        <div className="grd_c10">
                                            <Icon1 />
                                        </div>
                                        <div className="grd_c11">{terms[`MSG.${methodInfo.value}.Good`]}</div>
                                        <div className="grd_c20">
                                            <Icon2 />
                                        </div>
                                        <div className="grd_c21">{terms[`MSG.${methodInfo.value}.Ok`]}</div>
                                        <div className="grd_c30">
                                            <Icon3 />
                                        </div>
                                        <div className="grd_c31">{terms[`MSG.${methodInfo.value}.Bad`]}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            }
        </>
    )
}
export default Ss8_2_3;